import {
  DataLinkBuiltInVars,
  DisplayValue,
  getFieldDisplayName,
  InterpolateFunction,
  locationUtil,
  ScopedVars,
} from '@grafana/data';
import { find } from 'lodash';
import { AggregatorType } from './types';
/**
 * Returns options list for pivot table categories
 */
export const getCategoriesOptions = (context: any) => {
  const categories: any[] = [];
  if (context && context.data && context.data.length > 0) {
    context.data[0].fields.forEach((item: any) => {
      categories.push({ name: item.name, displayName: getFieldDisplayName(item, context.data[0]) });
    });
  }
  return categories;
};

/**
 * Returns categories values for pivot categories filter
 */
export const getCategoriesUniqueValues = (context: any, categories: any[]) => {
  const categoriesValues: any = {};
  if (context && context.data && context.data.length > 0) {
    context.data[0].fields.forEach((item: any) => {
      const catItem = find(categories, ['name', item.name]);
      if (catItem) {
        categoriesValues[item.name] = {
          values: [],
          isShow: false,
        };
        item.values.toArray().map((val: string) => {
          if (!categoriesValues[item.name].values.includes(val)) {
            categoriesValues[item.name].values.push(val);
          }
        });
      }
    });
  }
  return categoriesValues;
};

/**
 * encode string according to RFC 3986; in contrast to encodeURIComponent()
 * also the sub-delims "!", "'", "(", ")" and "*" are encoded;
 * unicode handling uses UTF-8 as in ECMA-262.
 */
function encodeURIComponentStrict(str: string) {
  return encodeURIComponent(str).replace(/[!'()*]/g, (c) => {
    return '%' + c.charCodeAt(0).toString(16).toUpperCase();
  });
}

/**
 * Replace URL variables with relevant data.
 */
export const enhanceDataLinks = (
  rowAttrs: any[],
  rowKeys: string[],
  colAttrs: any[],
  colKeys: string[],
  link: any,
  replaceVariables: InterpolateFunction
) => {
  const scopedVars: ScopedVars = {};
  const variablesQuery = locationUtil.getVariablesUrlParams();
  const timeRangeUrl = locationUtil.getTimeRangeUrlParams();

  scopedVars[DataLinkBuiltInVars.includeVars] = { text: variablesQuery, value: variablesQuery };
  scopedVars[DataLinkBuiltInVars.keepTime] = {
    text: timeRangeUrl,
    value: timeRangeUrl,
  };
  scopedVars[DataLinkBuiltInVars.timeRangeFrom] = {
    text: timeRangeUrl?.split('&')[0],
    value: timeRangeUrl?.split('&')[0],
  };
  scopedVars[DataLinkBuiltInVars.timeRangeTo] = {
    text: timeRangeUrl?.split('&')[1],
    value: timeRangeUrl?.split('&')[1],
  };

  let URL = replaceVariables(link.url, scopedVars);

  const filtersObject = {};
  rowKeys.forEach((key, index) => {
    // Because of Subtotal rows, which are not actual data, getting undefined hence adding below check.
    if (!rowAttrs[index]) {
      return;
    }
    // @ts-ignore
    filtersObject[rowAttrs[index].name] = key;
  });
  colKeys.forEach((key, index) => {
    // @ts-ignore
    filtersObject[colAttrs[index].name] = key;
  });

  Object.keys(filtersObject).forEach((cat) => {
    // @ts-ignore
    URL = URL.replace('$(' + cat + ')', encodeURIComponentStrict(filtersObject[cat]));
  });

  return { ...link, url: URL };
};

/**
 * Returns series data in CSV format for pivot table.
 */
export const parseData = (seriesData: any, options: any) => {
  const overrideAllowed = (field) => {
    return !options?.categories?.valueCategory?.find((item) => item.name === field.name);
  };
  const data: any = {};
  seriesData.series[0].fields.forEach((item: any) => {
    data[item.name] = seriesData.series[0].length
      ? Array(seriesData.series[0].length)
          .fill(null)
          .map((_, index) => {
            const itemVal = item.values.get(index);
            if (!overrideAllowed(item)) {
              return itemVal;
            }
            const displayValueObj: DisplayValue | undefined = item.display?.(itemVal);
            return displayValueObj && displayValueObj.text !== 'NaN' ? displayValueObj.text : itemVal;
          })
      : [];
  });
  const dataAsSCV: any[] = [];
  Object.keys(data).forEach((key) => {
    if (!dataAsSCV[0]) {
      dataAsSCV[0] = [];
    }
    dataAsSCV[0].push(key);
    data[key].forEach((val: any, index: number) => {
      const position = index + 1;
      if (!dataAsSCV[position]) {
        dataAsSCV[position] = [];
      }
      dataAsSCV[position].push(val);
    });
  });
  return dataAsSCV;
};

export const isDecimalAllowed = (aggType) => {
  return ![AggregatorType.COUNT, AggregatorType.MAX, AggregatorType.MIN, AggregatorType.MIN].includes(aggType);
};
