import PropTypes from 'prop-types';
import { AggregatorType, LIMITS, SUB_TOTAL_KEY } from 'types';
import { getDisplayProcessor, urlUtil } from '@grafana/data';
import { useTheme2 } from '@grafana/ui';

export const getDelimiter = () => {
  return '=4f0c7760-357b-11ed-885a-1d0d442e0042=';
};

export const addSeparators = function (nStr, thousandsSep, decimalSep) {
  const x = String(nStr).split('.');
  let x1 = x[0];
  const x2 = x.length > 1 ? decimalSep + x[1] : '';
  const rgx = /(\d+)(\d{3})/;
  while (rgx.test(x1)) {
    x1 = x1.replace(rgx, `$1${thousandsSep}$2`);
  }
  return x1 + x2;
};

const numberFormat = function (opts_in) {
  const defaults = {
    digitsAfterDecimal: 0,
    scaler: 1,
    thousandsSep: ',',
    decimalSep: '.',
    prefix: '',
    suffix: '',
  };
  const opts = Object.assign({}, defaults, opts_in);
  return function (x) {
    if (Array.isArray(x)) {
      let res = [];
      x.forEach((v) => {
        const result = addSeparators(
          (opts.scaler * v).toFixed(opts.digitsAfterDecimal),
          opts.thousandsSep,
          opts.decimalSep
        );
        res.push(`${opts.prefix}${result}${opts.suffix}`);
      });
      return res.join(' / ');
    }
    if ((!Array.isArray(x) && isNaN(x)) || !isFinite(x)) {
      return '';
    }
    if (!Array.isArray(x)) {
      const result = addSeparators(
        (opts.scaler * x).toFixed(opts.digitsAfterDecimal),
        opts.thousandsSep,
        opts.decimalSep
      );
      return `${opts.prefix}${result}${opts.suffix}`;
    }
  };
};

const rx = /(\d+)|(\D+)/g;
const rd = /\d/;
const rz = /^0/;
const naturalSort = (as, bs) => {
  // nulls first
  if (bs !== null && as === null) {
    return -1;
  }
  if (as !== null && bs === null) {
    return 1;
  }

  // then raw NaNs
  if (typeof as === 'number' && isNaN(as)) {
    return -1;
  }
  if (typeof bs === 'number' && isNaN(bs)) {
    return 1;
  }

  // numbers and numbery strings group together
  const nas = Number(as);
  const nbs = Number(bs);
  if (nas < nbs) {
    return -1;
  }
  if (nas > nbs) {
    return 1;
  }

  // within that, true numbers before numbery strings
  if (typeof as === 'number' && typeof bs !== 'number') {
    return -1;
  }
  if (typeof bs === 'number' && typeof as !== 'number') {
    return 1;
  }
  if (typeof as === 'number' && typeof bs === 'number') {
    return 0;
  }

  // 'Infinity' is a textual number, so less than 'A'
  if (isNaN(nbs) && !isNaN(nas)) {
    return -1;
  }
  if (isNaN(nas) && !isNaN(nbs)) {
    return 1;
  }

  // finally, "smart" string sorting per http://stackoverflow.com/a/4373421/112871
  let a = String(as);
  let b = String(bs);
  if (a === b) {
    return 0;
  }
  if (!rd.test(a) || !rd.test(b)) {
    return a > b ? 1 : -1;
  }

  // special treatment for strings containing digits
  a = a.match(rx);
  b = b.match(rx);
  while (a.length && b.length) {
    const a1 = a.shift();
    const b1 = b.shift();
    if (a1 !== b1) {
      if (rd.test(a1) && rd.test(b1)) {
        return a1.replace(rz, '.0') - b1.replace(rz, '.0');
      }
      return a1 > b1 ? 1 : -1;
    }
  }
  return a.length - b.length;
};

const sortAs = function (order) {
  const mapping = {};

  // sort lowercased keys similarly
  const l_mapping = {};
  for (const i in order) {
    const x = order[i];
    mapping[x] = i;
    if (typeof x === 'string') {
      l_mapping[x.toLowerCase()] = i;
    }
  }
  return function (a, b) {
    if (a in mapping && b in mapping) {
      return mapping[a] - mapping[b];
    } else if (a in mapping) {
      return -1;
    } else if (b in mapping) {
      return 1;
    } else if (a in l_mapping && b in l_mapping) {
      return l_mapping[a] - l_mapping[b];
    } else if (a in l_mapping) {
      return -1;
    } else if (b in l_mapping) {
      return 1;
    }
    return naturalSort(a, b);
  };
};

const getSort = function (sorters, attr) {
  if (sorters) {
    if (typeof sorters === 'function') {
      const sort = sorters(attr);
      /* istanbul ignore next */
      if (typeof sort === 'function') {
        return sort;
      }
    } else if (attr in sorters) {
      /* istanbul ignore next */
      return sorters[attr];
    }
  }
  return naturalSort;
};

// aggregator templates default to US number formatting but this is overrideable
const usFmt = numberFormat();
const usFmtInt = numberFormat({ digitsAfterDecimal: 0 });
const usFmtPct = numberFormat({
  digitsAfterDecimal: 1,
  scaler: 100,
  suffix: '%',
});

const aggregatorTemplates = {
  count(formatter = usFmtInt) {
    return () =>
      function () {
        return {
          count: 0,
          push() {
            this.count++;
          },
          value() {
            return this.count;
          },
          format: formatter,
        };
      };
  },

  sum() {
    return function (...x) {
      return function () {
        return {
          sum: 0,
          sumArr: [],
          push(record) {
            if (x[0].length > 1) {
              x[0].forEach((v, i) => {
                if (!this.sumArr[i]) {
                  this.sumArr[i] = 0;
                }
                this.sumArr[i] += parseFloat(record[x[0][i].name]);
              });
              this.sum = this.sumArr;
            }

            if (x[0].length === 1 && x[0][0] && !isNaN(parseFloat(record[x[0][0].name]))) {
              this.sum += parseFloat(record[x[0][0].name]);
            } else if (x[0] && !isNaN(parseFloat(record[x[0].name]))) {
              this.sum += parseFloat(record[x[0].name]);
            }
          },
          value() {
            return this.sum;
          },
          format: numberFormat({ digitsAfterDecimal: x[1] }),
        };
      };
    };
  },

  extremes(mode, formatter = usFmt) {
    return function (...attr) {
      return function (data) {
        return {
          val: null,
          valArr: [],
          sorter: getSort(typeof data !== 'undefined' ? data.sorters : null, attr.name),
          push(record) {
            if (attr[0].length === 1) {
              let x = record[attr[0][0].name];
              if (['min', 'max'].includes(mode)) {
                x = parseFloat(x);
                if (!isNaN(x)) {
                  this.val = Math[mode](x, this.val !== null ? this.val : x);
                }
              }
            }
            if (attr[0].length > 1) {
              attr[0].forEach((v, i) => {
                if (!this.valArr[i]) {
                  this.valArr[i] = null;
                }
                let x = record[attr[0][i].name];
                if (!isNaN(x)) {
                  this.valArr[i] = Math[mode](x, this.valArr[i] !== null ? this.valArr[i] : x);
                }
              });
              this.val = this.valArr;
            }
          },
          value() {
            return this.val;
          },
          format(x) {
            if (!Array.isArray(x) && isNaN(x)) {
              return x;
            } else if (Array.isArray(x)) {
              /* istanbul ignore next */
              x.forEach((v) => {
                if (isNaN(v)) {
                  return v;
                }
              });
              return formatter(x);
            } else {
              return formatter(x);
            }
          },
        };
      };
    };
  },

  quantile(q) {
    return function (...attr) {
      return function () {
        return {
          vals: [],
          push(record) {
            if (attr[0].length === 1) {
              const x = attr[0][0] ? parseFloat(record[attr[0][0].name]) : null;
              if (!isNaN(x)) {
                this.vals.push(x);
              }
            }
            if (attr[0].length > 1) {
              attr[0].forEach((v, i) => {
                const x = attr[0][i] ? parseFloat(record[attr[0][i].name]) : null;
                if (!isNaN(x)) {
                  if (!this.vals[i]) {
                    this.vals[i] = [];
                  }
                  this.vals[i].push(x);
                }
              });
            }
          },
          value() {
            if (this.vals.length === 0) {
              return '';
            }
            if (Array.isArray(this.vals[0])) {
              const arr = [];
              this.vals.forEach((v) => {
                v.sort((a, b) => a - b);
                const i = (v.length - 1) * q;
                arr.push((v[Math.floor(i)] + v[Math.ceil(i)]) / 2.0);
              });
              return arr;
            } else {
              this.vals.sort((a, b) => a - b);
              const i = (this.vals.length - 1) * q;
              return (this.vals[Math.floor(i)] + this.vals[Math.ceil(i)]) / 2.0;
            }
          },
          format: numberFormat({ digitsAfterDecimal: attr[1] }),
        };
      };
    };
  },

  runningStat(mode = 'mean', ddof = 1, formatter = usFmt) {
    return function (...attr) {
      return function () {
        return {
          n: 0.0,
          m: 0.0,
          s: 0.0,
          valArr: [],
          fvalArr: [],
          push(record) {
            this.n += 1.0;
            if (attr[0].length === 1) {
              const x = attr[0][0] ? parseFloat(record[attr[0][0].name]) : null;
              if (isNaN(x)) {
                return;
              }
              // this.n += 1.0;
              if (this.n === 1.0) {
                this.m = x;
              }
              const m_new = this.m + (x - this.m) / this.n;
              this.s = this.s + (x - this.m) * (x - m_new);
              this.m = m_new;
            }
            if (attr[0].length > 1) {
              let n = this.n;
              attr[0].forEach((v, i) => {
                if (!this.valArr[i]) {
                  this.valArr[i] = {
                    m: 0.0,
                    s: 0.0,
                  };
                }
                const x = attr[0][i] ? parseFloat(record[attr[0][i].name]) : null;
                if (isNaN(x)) {
                  return;
                }
                if (n === 1.0) {
                  this.valArr[i].m = x;
                }
                const m_new = this.valArr[i].m + (x - this.valArr[i].m) / n;
                this.valArr[i].s = this.valArr[i].s + (x - this.valArr[i].m) * (x - m_new);
                this.valArr[i].m = m_new;
              });
            }
          },
          value() {
            if (mode === 'mean' && !this.valArr.length) {
              if (this.n === 0) {
                return 0;
              }
              return this.m;
            } else if (mode === 'mean' && this.valArr.length) {
              this.valArr.forEach((v, i) => {
                if (!this.fvalArr[i]) {
                  this.fvalArr[i] = 0;
                }
                if (this.n === 0) {
                  this.fvalArr[i] = 0;
                } else {
                  this.fvalArr[i] = v.m;
                }
              });
              return this.fvalArr;
            }
            if (this.n <= ddof && !this.valArr.length) {
              return 0;
            }
            /* istanbul ignore next */
            if (this.n <= ddof && this.valArr.length) {
              this.valArr.forEach((v, i) => {
                this.fvalArr[i] = 0;
              });
            }
          },
          format: numberFormat({ digitsAfterDecimal: attr[1] }),
        };
      };
    };
  },

  fractionOf(wrapped, type = 'total') {
    return (attr, dec) =>
      function (data, rowKey, colKey) {
        return {
          selector: { total: [[], []], row: [rowKey, []], col: [[], colKey] }[type],
          inner: wrapped(...Array.from([attr] || []))(data, rowKey, colKey),
          push(record) {
            this.inner.push(record);
          },
          format: numberFormat({ digitsAfterDecimal: dec, scaler: 100, suffix: '%' }),
          value() {
            const innerVal =
              data.getAggregator(...Array.from(this.selector || [])).innerVal ||
              data.getAggregator(...Array.from(this.selector || [])).inner?.value();
            const val = this.inner.value() / innerVal;
            if (!val) {
              return '';
            }
            return val;
          },
        };
      };
  },
};

aggregatorTemplates.max = (f) => aggregatorTemplates.extremes('max', f);
aggregatorTemplates.min = (f) => aggregatorTemplates.extremes('min', f);
aggregatorTemplates.median = (f) => aggregatorTemplates.quantile(0.5, f);
aggregatorTemplates.average = (f) => aggregatorTemplates.runningStat('mean', 1, f);

// default aggregators & renderers use US naming and number formatting
const aggregators = ((tpl) => ({
  Count: tpl.count(usFmtInt),
  Sum: tpl.sum(usFmt),
  Average: tpl.average(usFmt),
  Median: tpl.median(usFmt),
  Minimum: tpl.min(usFmt),
  Maximum: tpl.max(usFmt),
  'Sum as a fraction of Totals': tpl.fractionOf(tpl.sum(), 'total', usFmtPct),
  'Sum as a fraction of rows': tpl.fractionOf(tpl.sum(), 'row', usFmtPct),
  'Sum as a fraction of columns': tpl.fractionOf(tpl.sum(), 'col', usFmtPct),
  'Count as a fraction of Totals': tpl.fractionOf(tpl.count(), 'total', usFmtPct),
  'Count as a fraction of rows': tpl.fractionOf(tpl.count(), 'row', usFmtPct),
  'Count as a fraction of columns': tpl.fractionOf(tpl.count(), 'col', usFmtPct),
}))(aggregatorTemplates);

/*
Data Model class
*/

class PivotData {
  constructor(inputProps = {}) {
    this.props = Object.assign({}, PivotData.defaultProps, inputProps);
    PropTypes.checkPropTypes(PivotData.propTypes, this.props, 'prop', 'PivotData');

    const urlParams = urlUtil.getUrlSearchParams();
    this.colMax = this.props.colMax ?? LIMITS.DEFAULT_COL_LIMIT;
    this.rowMax = this.props.rowMax ?? LIMITS.DEFAULT_ROW_LIMIT;

    this.tree = new Map();
    this.rowKeys = [];
    this.colKeys = [];
    this.rowTotals = new Map();
    this.colTotals = new Map();
    this.sorted = false;
    this.digitsAfterDecimal = this.props.fieldConfig?.defaults.decimals;
    this.aggregator = this.props.aggregators[this.props.aggregatorType](this.props.vals, this.digitsAfterDecimal);
    this.allTotal = this.aggregator(this, [], []);

    // Fields related to SubTotal Feature.
    this.showSubTotals = this.props.showSubTotals;
    this.subTotalColumnIndex = this.props.subTotalColumn;
    this.colSubTotals = new Map();

    // iterate through input, accumulating data for cells
    PivotData.forEachRecord(this.props.data, this.props.derivedAttributes, (record) => {
      if (this.filter(record)) {
        this.processRecord(record);
      }
    });

    // resetting allTotal
    this.allTotal = {
      innerVal: this.allTotal.inner ? this.allTotal.inner.value() : null,
      value: this.allTotal.value(),
      format: this.allTotal.format(this.allTotal.value()),
    };

    // If SubTotal is enabled then only calculate the logic and add the rows. Adding the subTotal calculation
    // row in the map after checking the next row size.
    if (this.showSubTotals && this.props.rows.length > 1) {
      const tempRowKeys = [];
      let subTotalRecord = undefined;
      let nextSubTotalFlatKey = undefined;
      this.rowKeys.forEach((rowKey, index) => {
        let subTotalFlatKey = rowKey.slice(0, this.subTotalColumnIndex + 1).join(String.fromCharCode(0));
        let lastRow = index === this.rowKeys.length - 1;
        if (this.rowKeys.hasOwnProperty(index + 1)) {
          nextSubTotalFlatKey = this.rowKeys[index + 1]
            .slice(0, this.subTotalColumnIndex + 1)
            .join(String.fromCharCode(0));
        }
        // Check if the next row is present and the subTotalKey is different then we have to add the subtotal row
        // or if the record is the last row then also we have to add the Subtotal Row.
        if (this.rowKeys.hasOwnProperty(index + 1) || lastRow) {
          subTotalRecord = [...rowKey];
        }
        tempRowKeys.push(rowKey);
        if (nextSubTotalFlatKey && (subTotalFlatKey !== nextSubTotalFlatKey || lastRow)) {
          subTotalRecord = subTotalRecord.slice(0, this.subTotalColumnIndex + 1);
          subTotalRecord[this.subTotalColumnIndex + 1] = SUB_TOTAL_KEY;
          const flatRowKey = subTotalRecord.join(String.fromCharCode(0));
          tempRowKeys.push(subTotalRecord);
          if (this.colSubTotals.get(flatRowKey)) {
            this.rowTotals.set(flatRowKey, this.colSubTotals.get(flatRowKey));
          }
        }
      });

      this.rowKeys = tempRowKeys;
    }

    this.refineValues(this.rowTotals);
    this.refineValues(this.colTotals);
    this.refineValues(this.colSubTotals);

    this.tree.forEach((val, key) => {
      if (!val) {
        return;
      }
      this.tree.set(key, {
        innerVal: val.inner ? val.inner.value() : null,
        value: val.value(),
        format: val.format(val.value()),
      });
    });
  }

  refineValues(map) {
    map.forEach((val, key) => {
      map.set(key, {
        innerVal: val.inner ? val.inner.value() : null,
        value: val.value(),
        format: val.format(val.value()),
      });
    });
  }

  filter(record) {
    for (const k in this.props.valueFilter) {
      if (this.props.valueFilter[k].includes(record[k])) {
        return false;
      }
    }
    return true;
  }

  arrSort(attrs, asc) {
    const sortersArr = (() => {
      const result = [];
      for (let a of attrs) {
        result.push(getSort(this.props.sorters, a));
      }
      return result;
    })();
    return function (a, b) {
      for (const i of Object.keys(sortersArr || {})) {
        const sorter = sortersArr[i];
        // Little trick introduced to always show the Subtotal row last in the rowSpan when sorting is
        // applied using panel Sort Order option. We only support 'Key asc' & 'Key desc' for the Subtotal row.
        // Using ~ and white space as a first character to sort the subtotal according to ASC & DESC.
        const str1 = a[i] === SUB_TOTAL_KEY ? (asc ? '~' + a[i] : ' ' + a[i]) : a[i];
        const str2 = b[i] === SUB_TOTAL_KEY ? (asc ? '~' + b[i] : ' ' + b[i]) : b[i];
        const comparison = asc ? sorter(str1, str2) : sorter(str2, str1);
        if (comparison !== 0) {
          return comparison;
        }
      }
      return 0;
    };
  }

  sortKeys() {
    if (!this.sorted) {
      this.sorted = true;
      const v = (r, c) => this.getAggregator(r, c).value;
      switch (this.props.rowOrder) {
        case 'none':
          break;
        case 'key_a_to_z':
          this.rowKeys.sort(this.arrSort(this.props.rows, true));
          break;
        case 'key_z_to_a':
          this.rowKeys.sort(this.arrSort(this.props.rows, false));
          break;
        case 'value_a_to_z':
          this.rowKeys.sort((a, b) => naturalSort(v(a, []), v(b, [])));
          break;
        case 'value_z_to_a':
          this.rowKeys.sort((a, b) => -naturalSort(v(a, []), v(b, [])));
          break;
      }
      switch (this.props.colOrder) {
        case 'none':
          break;
        case 'key_a_to_z':
          this.colKeys.sort(this.arrSort(this.props.cols, true));
          break;
        case 'key_z_to_a':
          this.colKeys.sort(this.arrSort(this.props.cols, false));
          break;
        case 'value_a_to_z':
          this.colKeys.sort((a, b) => naturalSort(v([], a), v([], b)));
          break;
        case 'value_z_to_a':
          this.colKeys.sort((a, b) => -naturalSort(v([], a), v([], b)));
          break;
      }
    }
  }

  getColKeys() {
    this.sortKeys();
    this.colsHidden = this.colKeys.length > this.colMax ? true : false;
    return this.colKeys.length > this.colMax ? this.colKeys.slice(0, this.colMax) : this.colKeys;
  }

  getRowKeys() {
    this.sortKeys();
    this.rowsHidden = this.rowKeys.length > this.rowMax ? true : false;
    return this.rowKeys.length > this.rowMax ? this.rowKeys.slice(0, this.rowMax) : this.rowKeys;
  }

  getHideStatus() {
    return {
      colsHidden: this.colsHidden,
      rowsHidden: this.rowsHidden,
    };
  }

  processRecord(record) {
    // this code is called in a tight loop
    const colKey = [];
    const rowKey = [];
    const displayProcessor = getDisplayProcessor({
      field: {
        config: {
          unit: this.props.fieldConfig?.defaults.unit,
        },
      },
      theme: useTheme2(),
    });
    for (const x of this.props.cols) {
      const displayValueObj = displayProcessor(record[x.name]);
      const date = !isNaN(record[x.name]) ? new Date(record[x.name]) : Date.parse(record[x.name]);
      const value = isNaN(date)
        ? this.props.fieldConfig?.defaults.mappings?.find((v) => {
            return v.options[record[x.name]] ? true : false;
          })?.options[record[x.name]].text || record[x.name]
        : displayValueObj.text;
      colKey.push(x.name in record ? value : 'null');
    }
    for (const x of this.props.rows) {
      const displayValueObj = displayProcessor(record[x.name]);
      const date = !isNaN(record[x.name]) ? new Date(record[x.name]) : Date.parse(record[x.name]);
      const value = isNaN(date)
        ? this.props.fieldConfig?.defaults.mappings?.find((v) => {
            return v.options[record[x.name]] ? true : false;
          })?.options[record[x.name]].text || record[x.name]
        : displayValueObj.text;
      rowKey.push(x.name in record ? value : 'null');
    }
    const flatRowKey = rowKey.join(String.fromCharCode(0));
    const flatColKey = colKey.join(String.fromCharCode(0));

    this.allTotal.push(record);

    if (rowKey.length !== 0) {
      if (!this.rowTotals.get(flatRowKey)) {
        this.rowKeys.push(rowKey);
        this.rowTotals.set(flatRowKey, this.aggregator(this, rowKey, []));
      }
      this.rowTotals.get(flatRowKey).push(record);
    }

    if (colKey.length !== 0) {
      if (!this.colTotals.get(flatColKey)) {
        this.colKeys.push(colKey);
        this.colTotals.set(flatColKey, this.aggregator(this, [], colKey));
      }
      this.colTotals.get(flatColKey).push(record);
    }

    // Filling subTotal aggregator for each sub total key.
    if (this.showSubTotals && colKey.length !== 0 && rowKey.length !== 0) {
      const subTotalKey =
        rowKey.slice(0, this.subTotalColumnIndex + 1).join(String.fromCharCode(0)) + '\u{0000}' + SUB_TOTAL_KEY;
      if (!this.colSubTotals.get(subTotalKey)) {
        if (
          this.props.aggregatorType === AggregatorType.SUM_AS_FRANCTION_OF_COLUMNS ||
          this.props.aggregatorType === AggregatorType.COUNT_AS_FRANCTION_OF_COLUMNS
        ) {
          // For this 2 functions, final total is the reference point for calculation hence passing blank array for rowKey & colKey.
          this.colSubTotals.set(subTotalKey, this.aggregator(this, [], []));
        } else if (
          this.props.aggregatorType === AggregatorType.SUM_AS_FRANCTION_OF_ROWS ||
          this.props.aggregatorType === AggregatorType.COUNT_AS_FRANCTION_OF_ROWS
        ) {
          // For this 2 functions, sobTotal row total is the reference point hence passing subTotalRecord as a rowKey.
          const subTotalRecord = [...rowKey.slice(0, this.subTotalColumnIndex + 1)];
          subTotalRecord[this.subTotalColumnIndex + 1] = SUB_TOTAL_KEY;
          this.colSubTotals.set(subTotalKey, this.aggregator(this, subTotalRecord, colKey));
        } else {
          this.colSubTotals.set(subTotalKey, this.aggregator(this, rowKey, colKey));
        }
      }
      this.colSubTotals.get(subTotalKey).push(record);
    }

    if (colKey.length !== 0 && rowKey.length !== 0) {
      const compKey = flatRowKey + getDelimiter() + flatColKey;
      if (!this.tree.get(compKey)) {
        this.tree.set(compKey, this.aggregator(this, rowKey, colKey));
      }
      this.tree.get(compKey).push(record);
    }

    // Adding SubTotal Calculation for each row and column in the tree to access it in HTML.
    if (this.showSubTotals && colKey.length !== 0 && rowKey.length !== 0) {
      const subTotalKey =
        rowKey.slice(0, this.subTotalColumnIndex + 1).join(String.fromCharCode(0)) +
        '\u{0000}' +
        SUB_TOTAL_KEY +
        getDelimiter() +
        flatColKey;
      if (!this.tree.get(subTotalKey)) {
        if (
          this.props.aggregatorType === AggregatorType.SUM_AS_FRANCTION_OF_ROWS ||
          this.props.aggregatorType === AggregatorType.COUNT_AS_FRANCTION_OF_ROWS
        ) {
          // For this 2 functions, sobTotal row total is the reference point for calculation hence passing subTotalRecord as a rowKey.
          const subTotalRecord = [...rowKey.slice(0, this.subTotalColumnIndex + 1)];
          subTotalRecord[this.subTotalColumnIndex + 1] = SUB_TOTAL_KEY;
          this.tree.set(subTotalKey, this.aggregator(this, subTotalRecord, colKey));
        } else {
          this.tree.set(subTotalKey, this.aggregator(this, rowKey, colKey));
        }
      }
      this.tree.get(subTotalKey).push(record);
    }
  }

  getAggregator(rowKey, colKey) {
    let agg;
    const flatRowKey = rowKey.join(String.fromCharCode(0));
    const flatColKey = colKey.join(String.fromCharCode(0));
    if (rowKey.length === 0 && colKey.length === 0) {
      agg = this.allTotal;
    } else if (rowKey.length === 0) {
      agg = this.colTotals.get(flatColKey);
    } else if (colKey.length === 0) {
      agg = this.rowTotals.get(flatRowKey);
    } else {
      const compKey = flatRowKey + getDelimiter() + flatColKey;
      agg = this.tree.get(compKey);
    }
    const aggregatorType = this.props.aggregatorType;
    const decimals = this.props.fieldConfig?.defaults.decimals;
    return (
      agg || {
        value: null,
        format: (() => {
          const defaultCount = 0;
          switch (aggregatorType) {
            case AggregatorType.COUNT:
            case AggregatorType.MIN:
            case AggregatorType.MAX:
              return defaultCount;

            case AggregatorType.SUM:
            case AggregatorType.AVERAGE:
            case AggregatorType.MEDIAN:
              return defaultCount.toFixed(decimals);

            case AggregatorType.SUM_AS_FRANCTION_OF_COLUMNS:
            case AggregatorType.SUM_AS_FRANCTION_OF_ROWS:
            case AggregatorType.SUM_AS_FRANCTION_OF_TOTAL:
            case AggregatorType.COUNT_AS_FRANCTION_OF_COLUMNS:
            case AggregatorType.COUNT_AS_FRANCTION_OF_ROWS:
            case AggregatorType.COUNT_AS_FRANCTION_OF_TOTAL:
              return defaultCount.toFixed(decimals) + '%';
          }
        })(),
      }
    );
  }
}

// can handle arrays or jQuery selections of tables
PivotData.forEachRecord = function (input, derivedAttributes, f) {
  let addRecord, record;
  if (Object.getOwnPropertyNames(derivedAttributes).length === 0) {
    addRecord = f;
  } else {
    /* istanbul ignore next */
    addRecord = function (record) {
      for (const k in derivedAttributes) {
        const derived = derivedAttributes[k](record);
        if (derived !== null) {
          record[k] = derived;
        }
      }
      return f(record);
    };
  }

  // if it's a function, have it call us back
  if (typeof input === 'function') {
    return input(addRecord);
  } else if (Array.isArray(input)) {
    if (Array.isArray(input[0])) {
      // array of arrays
      return (() => {
        const result = [];
        for (const i of Object.keys(input || {})) {
          const compactRecord = input[i];
          if (i > 0) {
            record = {};
            for (const j of Object.keys(input[0] || {})) {
              const k = input[0][j];
              record[k] = compactRecord[j];
            }
            result.push(addRecord(record));
          }
        }
        return result;
      })();
    }

    // array of objects
    /* istanbul ignore next */
    return (() => {
      const result1 = [];
      for (record of Array.from(input)) {
        result1.push(addRecord(record));
      }
      return result1;
    })();
  }
  throw new Error('unknown input format');
};

PivotData.defaultProps = {
  aggregators: aggregators,
  cols: [],
  rows: [],
  vals: [],
  aggregatorType: 'Count',
  sorters: {},
  valueFilter: {},
  rowOrder: 'key_a_to_z',
  colOrder: 'key_a_to_z',
  derivedAttributes: {},
  width: '0px',
  height: '0px',
};

PivotData.propTypes = {
  data: PropTypes.oneOfType([PropTypes.array, PropTypes.object, PropTypes.func]).isRequired,
  aggregatorType: PropTypes.string,
  cols: PropTypes.arrayOf(PropTypes.any),
  rows: PropTypes.arrayOf(PropTypes.any),
  vals: PropTypes.arrayOf(PropTypes.any),
  valueFilter: PropTypes.objectOf(PropTypes.arrayOf(PropTypes.any)),
  sorters: PropTypes.oneOfType([PropTypes.func, PropTypes.objectOf(PropTypes.func)]),
  derivedAttributes: PropTypes.objectOf(PropTypes.func),
  rowOrder: PropTypes.oneOf(['none', 'key_a_to_z', 'key_z_to_a', 'value_a_to_z', 'value_z_to_a']),
  colOrder: PropTypes.oneOf(['none', 'key_a_to_z', 'key_z_to_a', 'value_a_to_z', 'value_z_to_a']),
};

export { aggregatorTemplates, aggregators, naturalSort, numberFormat, getSort, sortAs, PivotData };
