import { FieldConfigProperty, FieldOverrideContext, PanelPlugin } from '@grafana/data';
import { CrossTabPanel } from 'CrossTabPanel';
import { PivotCategoriesEditor } from './customPanelEditors/PivotCategoriesEditor';
import { AggregatorType, CellDisplayMode, Options, LIMITS, SortingOrders } from './types';

export const plugin = new PanelPlugin<Options>(CrossTabPanel)
  .useFieldConfig({
    standardOptions: {
      [FieldConfigProperty.Thresholds]: { defaultValue: 0 },
      [FieldConfigProperty.Links]: { defaultValue: 0 },
      [FieldConfigProperty.Mappings]: { defaultValue: null },
      [FieldConfigProperty.Decimals]: { defaultValue: 0 },
    },
    disableStandardOptions: [
      FieldConfigProperty.Min,
      FieldConfigProperty.Max,
      FieldConfigProperty.Color,
      FieldConfigProperty.NoValue,
      FieldConfigProperty.DisplayName,
      'fieldMinMax' as FieldConfigProperty,
    ],
    useCustomConfig: (builder) => {
      /* istanbul ignore next */
      builder.addSelect({
        path: 'cellDisplayMode',
        name: 'Cell display mode',
        description: 'Color text or background',
        defaultValue: CellDisplayMode.None,
        settings: {
          options: [
            { value: CellDisplayMode.None, label: 'No coloring' },
            { value: CellDisplayMode.ColorText, label: 'Color text' },
            { value: CellDisplayMode.ColorBackground, label: 'Color background' },
          ],
        },
      });
    },
  })
  .setPanelOptions((builder) => {
    /* istanbul ignore next */
    return builder
      .addSelect({
        path: 'aggregatorType',
        name: 'Aggregate function',
        description: 'Select the aggregate function',
        defaultValue: 'Sum',
        settings: {
          options: [],
          getOptions: async (context: FieldOverrideContext) => {
            const options: Array<{ value: string; label: string }> = [];
            Object.keys(AggregatorType).forEach((key) => {
              // @ts-ignore
              options.push({ value: AggregatorType[key], label: AggregatorType[key] });
            });
            return options;
          },
        },
      })
      .addNumberInput({
        path: 'colMax',
        name: 'Max columns allowed',
        description: 'Set maximum number of columns allowed for the pivot table',
        defaultValue: LIMITS.DEFAULT_COL_LIMIT,
        settings: {
          max: LIMITS.VIEW_MODE_NODE_LIMIT,
          min: 1,
        },
      })
      .addNumberInput({
        path: 'rowMax',
        name: 'Max rows allowed',
        description: 'Set maximum number of rows allowed for the pivot table',
        defaultValue: LIMITS.DEFAULT_ROW_LIMIT,
        settings: {
          max: LIMITS.VIEW_MODE_NODE_LIMIT,
          min: 1,
        },
      })
      .addCustomEditor({
        id: 'categories',
        path: 'categories',
        name: 'Pivot categories',
        description: 'Select the categories that will appear in each section (row, column, and value)',
        editor: PivotCategoriesEditor,
        defaultValue: {
          rowsCategories: [],
          columnsCategories: [],
          valueCategory: [],
          categoriesValueFilter: {},
          rowOrder: SortingOrders.KEY_ASC,
          colOrder: 'none',
          filterable: {},
        },
      })
      .addBooleanSwitch({
        path: 'showTotals',
        name: 'Show the Total columns and row',
        category: ['Total'],
        description: 'Show/Hide Total columns and row values',
        defaultValue: true,
      })
      .addBooleanSwitch({
        path: 'showRowPercentage',
        name: 'Show Percentage of the Total row',
        category: ['Total'],
        description: 'Show/Hide Percentage of the Total row',
        defaultValue: false,
        showIf: (currentConfig) => {
          return (
            !currentConfig.aggregatorType.includes('Sum as') &&
            !currentConfig.aggregatorType.includes('Count as') &&
            currentConfig.showTotals &&
            currentConfig.categories.valueCategory.length < 2
          );
        },
      })
      .addBooleanSwitch({
        path: 'showColumnPercentage',
        name: 'Show Percentage of the Total column',
        category: ['Total'],
        description: 'Show/Hide Percentage of the Total column',
        defaultValue: false,
        showIf: (currentConfig) => {
          return (
            !currentConfig.aggregatorType.includes('Sum as') &&
            !currentConfig.aggregatorType.includes('Count as') &&
            currentConfig.showTotals &&
            currentConfig.categories.valueCategory.length < 2
          );
        },
      })
      .addBooleanSwitch({
        path: 'showSubTotals',
        name: 'Show the Subtotal row',
        category: ['Total'],
        description: 'Show/Hide Subtotal row values',
        defaultValue: false,
        showIf: (currentConfig) => {
          // Hide SubTotal and reset if Total is disabled. Also ther eis no point if sort order is Total Asc and Desc
          // because we loss the context of calculation and showing the subtotal hence best thing will be to just disable it.
          const showSub =
            currentConfig.showTotals &&
            currentConfig.categories.rowsCategories.length > 1 &&
            currentConfig.categories.rowOrder !== SortingOrders.TOTAL_ASC &&
            currentConfig.categories.rowOrder !== SortingOrders.TOTAL_DESC;
          if (!showSub) {
            currentConfig.showSubTotals = false;
          }
          return showSub;
        },
      })
      .addSelect({
        path: 'subTotalColumn',
        name: 'Apply Subtotal for',
        category: ['Total'],
        description: 'Select the column for which you want to apply the Subtotal',
        defaultValue: 0,
        settings: {
          options: [],
          getOptions: async (context: FieldOverrideContext) => {
            const options: Array<{ value: number; label: string }> = [];
            const rowsCategories = context.options.categories.rowsCategories;
            rowsCategories.forEach((category, index) => {
              // Ignore the last field because we don't show the subtotal for last column.
              if (index === rowsCategories.length - 1) {
                return;
              }
              options.push({ value: index, label: category.displayName });
            });
            // If field is removed from rowFields then default 0 field should be set as subTotal column.
            if (!options.hasOwnProperty(context.options.subTotalColumn)) {
              context.options.subTotalColumn = 0;
            }
            return options;
          },
        },
        showIf: (currentConfig) => {
          return (
            currentConfig.showTotals &&
            currentConfig.showSubTotals &&
            currentConfig.categories.rowsCategories.length > 1
          );
        },
      })
      .addColorPicker({
        path: 'subTotalHeaderBgColor',
        name: 'Subtotal header background color',
        category: ['Total'],
        description: 'Select color for Subtotal header background',
        showIf: (currentConfig) => {
          return (
            currentConfig.showTotals &&
            currentConfig.showSubTotals &&
            currentConfig.categories.rowsCategories.length > 1
          );
        },
      })
      .addColorPicker({
        path: 'headerColor',
        name: 'Header text color',
        category: ['Text'],
        description: 'Select color for header text',
      })
      .addColorPicker({
        path: 'headerBgColor',
        category: ['Background'],
        name: 'Header background color',
        description: 'Select color for header background',
      })
      .addRadio({
        path: 'columnTextAlign',
        name: 'Column text align',
        category: ['Fields alignment'],
        defaultValue: 'left',
        settings: {
          options: [
            { value: 'left', label: 'Left' },
            { value: 'center', label: 'Center' },
            { value: 'right', label: 'Right' },
          ],
        },
      })
      .addRadio({
        path: 'rowTextAlign',
        name: 'Row text align',
        category: ['Fields alignment'],
        defaultValue: 'left',
        settings: {
          options: [
            { value: 'left', label: 'Left' },
            { value: 'center', label: 'Center' },
            { value: 'right', label: 'Right' },
          ],
        },
      })
      .addRadio({
        path: 'valueTextAlign',
        name: 'Value text align',
        category: ['Fields alignment'],
        defaultValue: 'right',
        settings: {
          options: [
            { value: 'left', label: 'Left' },
            { value: 'center', label: 'Center' },
            { value: 'right', label: 'Right' },
          ],
        },
      });
  });
