import React from 'react';
import { FilterItem } from './PivotCategoriesFilterItem';
import { getCategoriesUniqueValues } from '../CrossTabCtrl';
import { useTheme, Checkbox } from '@grafana/ui';
import { getLabelStyles } from '../styles';
import { cx } from '@emotion/css';

export interface PivotCategoriesFilterProps {
  context: any;
  rowCategories: any[];
  columnCategories: any[];
  categoriesValueFilter: any;
  value: any;
  onSelect: any;
  onChange: (cat: string, val: string, selected: boolean) => void;
}

export const PivotCategoriesFilter: React.FC<PivotCategoriesFilterProps> = ({
  context,
  rowCategories,
  columnCategories,
  categoriesValueFilter,
  value,
  onSelect,
  onChange,
}) => {
  const categoriesValues = getCategoriesUniqueValues(context, [...rowCategories, ...columnCategories]);
  return Object.keys(categoriesValues).length > 0
    ? renderFilterItems(categoriesValues, categoriesValueFilter, onChange, onSelect, value)
    : renderEmptyFilterItemsList();
};

const renderFilterItems = (
  categoriesValues: any,
  categoriesValueFilter: any,
  onChange: (cat: string, val: string, selected: boolean) => void,
  onSelect: any,
  value: any
) => {
  /* istanbul ignore next */
  return (
    <div style={{ maxHeight: '240px', overflowY: 'auto' }}>
      {Object.keys(categoriesValues).map((key) => {
        value.filterable[key] = {
          values: categoriesValues[key].values,
          isShow: value.filterable[key] ? value.filterable[key].isShow : false,
        };
        return (
          <div key={key} style={{ display: 'flex', flexDirection: 'row', flexWrap: 'wrap', width: '100%' }}>
            <div style={{ display: 'flex', flexDirection: 'column', flexBasis: '100%', flex: 0.9 }}>
              <FilterItem
                key={key}
                category={key}
                values={categoriesValues[key].values}
                categoriesValueFilter={categoriesValueFilter[key] ? categoriesValueFilter[key] : []}
                onChange={onChange}
              />
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                flexBasis: '100%',
                flex: 0.1,
                marginLeft: '15px',
                marginTop: '18px',
              }}
            >
              <Checkbox
                value={value.filterable[key].isShow}
                onChange={(e) => {
                  value.filterable[key].isShow = e.currentTarget.checked;
                  onSelect(value);
                }}
              />
            </div>
          </div>
          // )
        );
      })}
    </div>
  );
};

const renderEmptyFilterItemsList = () => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const theme = useTheme();
  const labelStyles = getLabelStyles(theme);
  return <label className={cx(labelStyles.description)}>{'No Row or Column categories selected'}</label>;
};
