import { GrafanaTheme, StandardEditorProps } from '@grafana/data';
import { ControlledCollapse, Icon, Select, useTheme, Input } from '@grafana/ui';
import { css, cx } from '@emotion/css';
import { find } from 'lodash';
import React, { useState } from 'react';
import { getCategoriesOptions, getCategoriesUniqueValues } from '../CrossTabCtrl';
import { getLabelStyles } from '../styles';
import { Categories, SortingOrders } from '../types';
import { PivotCategoriesFilter } from './PivotCategoriesFilter';
import { PivotCategoriesPicker } from './PivotCategoriesPicker';

interface EditorProps extends StandardEditorProps<Categories, any> {}

export interface CategoryItem {
  name: string;
  type: CategoryType;
}

export enum CategoryType {
  ROW = 'row',
  COLUMN = 'col',
  VALUE = 'val',
  NONE = 'none',
}

/* istanbul ignore next */
export const PivotCategoriesEditor: React.FC<EditorProps> = ({ value, context, onChange }) => {
  const [searchQuery, setSearchQuery] = useState('');
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const theme = useTheme();
  const styles = getStyles(theme);
  const labelStyles = getLabelStyles(theme);

  const suffix =
    searchQuery !== '' ? (
      <span className={styles.searchClear} onClick={() => setSearchQuery('')}>
        <Icon name="times" />
        Clear filter
      </span>
    ) : null;

  /*
   * get all categories from the plugin data
   */
  const categories = getCategoriesOptions(context);

  if (categories.length > 0) {
    updateCategoriesAccordingToRecievedData(value, categories, context);
  }
  React.useEffect(()=>{
    console.log(context)
  },[value, context])
  return (
    <>
      <ControlledCollapse label="Fields" collapsible={true}>
        <Input
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.currentTarget.value)}
          prefix={<Icon name="filter" className={styles.icon} />}
          suffix={suffix}
          placeholder="Filter categories"
        />
        <PivotCategoriesPicker
          categories={categories}
          filterQuery={searchQuery}
          rowCategories={value.rowsCategories}
          columnCategories={value.columnsCategories}
          valCategories={value.valueCategory}
          onSelect={(categories) =>
            updateOptionsAfterSelect(getCategoriesOptions(context), categories, value, onChange)
          }
          onChangeOrder={(catByTypes) => updateOptionsAfterChangeOrder(catByTypes, value, onChange)}
        />
      </ControlledCollapse>
      <ControlledCollapse label="Field filters" collapsible={true}>
        <PivotCategoriesFilter
          context={context}
          rowCategories={value.rowsCategories}
          columnCategories={value.columnsCategories}
          categoriesValueFilter={value.categoriesValueFilter}
          value={value}
          onSelect={onChange}
          onChange={(cat: string, val: string, checked: boolean) =>
            updateCategoriesValueFilter(cat, val, checked, value, onChange)
          }
        />
      </ControlledCollapse>
      <div className={styles.sortCollapseWrapper}>
        <ControlledCollapse label="Sort order" collapsible={true}>
          <label className={cx(labelStyles.description)}>{'Select the sorting order for columns'}</label>
          <div style={{ marginTop: '5px' }}>
            <Select
              options={categoriesSortingOptions}
              value={value.colOrder}
              onChange={(selectedOrder) => updateOptionsAfterChangeColsValuesOrder(selectedOrder, value, onChange)}
            />
          </div>
          <label className={cx(labelStyles.description)} style={{ marginTop: '20px' }}>
            {'Select the sorting order for rows'}
          </label>
          <div style={{ marginTop: '5px' }}>
            <Select
              options={categoriesSortingOptions}
              value={value.rowOrder}
              onChange={(selectedOrder) => updateOptionsAfterChangeRowsValuesOrder(selectedOrder, value, onChange)}
            />
          </div>
        </ControlledCollapse>
      </div>
    </>
  );
};

const categoriesSortingOptions = [
  { value: SortingOrders.NONE, label: 'None' },
  { value: SortingOrders.KEY_ASC, label: 'Key asc' },
  { value: SortingOrders.KEY_DESC, label: 'Key desc' },
  { value: SortingOrders.TOTAL_ASC, label: 'Totals asc' },
  { value: SortingOrders.TOTAL_DESC, label: 'Totals desc' },
];

/* istanbul ignore next */
const getStyles = (theme: GrafanaTheme) => {
  return {
    icon: css`
      color: ${theme.palette.gray33};
    `,
    wrapper: css`
      display: flex;
      flex-direction: column;
    `,
    searchClear: css`
      color: ${theme.palette.gray60};
      cursor: pointer;
    `,
    sortCollapseWrapper: css`
      > div > div:nth-child(2) {
        overflow: inherit;
        > div:nth-child(2) {
          overflow: inherit;
        }
      }
    `,
  };
};

/*
 * remove categories if not exist in plugin data (for renaming case or different categories in different time range)
 */
export const updateCategoriesAccordingToRecievedData = (value: any, categories: any, context: any) => {
  function updatedCategory(cat) {
    let catMap: any[] = [];
    cat.forEach((element) => {
      let found = find(categories, ['name', element?.name]);
      found && catMap.push(found);
    });
    return catMap;
  }
  value.rowsCategories = updatedCategory(value.rowsCategories);
  value.columnsCategories = updatedCategory(value.columnsCategories);
  value.valueCategory = updatedCategory(value.valueCategory);
  value.categoriesValues = getCategoriesUniqueValues(context, categories);
};

/*
 * Updated categories objects (rows, columns, and value) after selection
 */
export const updateOptionsAfterSelect = (
  catOpts: any,
  categories: CategoryItem[],
  options: any,
  onChange: (options: any) => void
) => {
  const colCategories: any[] = [];
  const rowCategories: any[] = [];
  const valCategories: any[] = [];
  categories.forEach((cat) => {
    switch (cat.type) {
      case CategoryType.COLUMN: {
        const found = find(catOpts, ['name', cat?.name]);
        found && colCategories.push(found);
        break;
      }
      case CategoryType.ROW: {
        const found = find(catOpts, ['name', cat?.name]);
        found && rowCategories.push(found);
        break;
      }
      case CategoryType.VALUE: {
        const found = find(catOpts, ['name', cat?.name]);
        found && valCategories.push(found);
        break;
      }
    }
  });
  options.rowsCategories = rowCategories;
  options.columnsCategories = colCategories;
  options.valueCategory = valCategories;
  onChange(options);
};

/*
 * Updated categories order in each section (rows, columns, and value)
 */
export const updateOptionsAfterChangeOrder = (catByTypes: any, options: any, onChange: (options: any) => void) => {
  const colCategories: any[] = [];
  const rowCategories: any[] = [];
  const valCategories: any[] = [];
  Object.keys(catByTypes).forEach((catType) => {
    switch (catType) {
      case CategoryType.COLUMN: {
        catByTypes[catType].forEach((cat: { key: string }) => colCategories.push(cat.key));
        break;
      }
      case CategoryType.ROW: {
        catByTypes[catType].forEach((cat: { key: string }) => rowCategories.push(cat.key));
        break;
      }
      case CategoryType.VALUE: {
        catByTypes[catType].forEach((cat: { key: string }) => valCategories.push(cat.key));
        break;
      }
    }
  });
  options.rowsCategories = rowCategories;
  options.columnsCategories = colCategories;
  options.valueCategory = valCategories;
  onChange(options);
};

/*
 * Updated 'rowOrder' object in options
 */
export const updateOptionsAfterChangeRowsValuesOrder = (
  rowOrder: any,
  options: any,
  onChange: (options: any) => void
) => {
  options.rowOrder = rowOrder.value;
  onChange(options);
};

/*
 * Updated 'colOrder' object in options
 */
export const updateOptionsAfterChangeColsValuesOrder = (
  colOrder: any,
  options: any,
  onChange: (options: any) => void
) => {
  options.colOrder = colOrder.value;
  onChange(options);
};

/*
 * Updated 'categoriesValueFilter' object to hide categories values in the pivot table
 */
export const updateCategoriesValueFilter = (
  cat: string,
  val: string,
  selected: boolean,
  options: any,
  onChange: (options: any) => void
) => {
  if (!options.categoriesValueFilter[cat]) {
    options.categoriesValueFilter[cat] = [];
  }
  const index = options.categoriesValueFilter[cat].indexOf(val);
  if (!selected) {
    if (index === -1) {
      options.categoriesValueFilter[cat].push(val);
    }
  } else {
    if (index !== -1) {
      options.categoriesValueFilter[cat].splice(index, 1);
    }
  }
  onChange(options);
};
