import { GrafanaTheme } from '@grafana/data';
import { Icon, useTheme } from '@grafana/ui';
import { cx } from '@emotion/css';
import React, { ButtonHTMLAttributes, PureComponent } from 'react';
import { getButtonStyles, getTrashIconStyle } from '../styles';
import { CategoryType } from './PivotCategoriesEditor';

interface PivotCategoryItemProps {
  key: string;
  categoryName: string;
  catType: CategoryType;
  onDragStart: any;
  index: number;
  quantity: number;
  moveUp: () => void;
  moveDown: () => void;
  removeItem: () => void;
}

export class PivotCategoryItem extends PureComponent<PivotCategoryItemProps> {
  render() {
    return (
      <div style={{ padding: '5px', width: 'fit-content' }} draggable onDragStart={this.props.onDragStart}>
        <CatItem
          categoryName={this.props.categoryName}
          catType={this.props.catType}
          index={this.props.index}
          quantity={this.props.quantity}
          moveUp={this.props.moveUp}
          moveDown={this.props.moveDown}
          removeItem={this.props.removeItem}
        />
      </div>
    );
  }
}

export interface StyleProps {
  theme: GrafanaTheme;
  size: string;
  variant: ItemVariant;
  hasIcon: boolean;
  hasText: boolean;
}

type ItemVariant = 'primary' | 'secondary' | 'destructive' | 'link';

type CommonProps = {
  categoryName: string;
  catType: CategoryType;
  index: number;
  quantity: number;
  moveUp: () => void;
  moveDown: () => void;
  removeItem: () => void;
};

type CatItemProps = CommonProps & ButtonHTMLAttributes<HTMLButtonElement>;

const CatItem = React.forwardRef<HTMLButtonElement, CatItemProps>(
  ({ categoryName, catType, index, quantity, moveUp, moveDown, removeItem, children, ...otherProps }, ref) => {
    const theme = useTheme();
    const buttonStyles = getButtonStyles({
      theme,
      size: 'sm',
      variant: 'secondary',
      hasText: children !== undefined,
      hasIcon: false,
    });
    const trashIconStyle = getTrashIconStyle(theme);

    return (
      <button className={cx(buttonStyles.button)} {...otherProps} ref={ref}>
        {catType === CategoryType.NONE || catType === CategoryType.VALUE || quantity < 2 ? (
          <>
            {catType !== CategoryType.NONE ? (
              <>
                <Icon className={trashIconStyle.trashIcon} name="trash-alt" onClick={removeItem} />
                <span style={{ marginLeft: '10px', cursor: 'grab' }}>{categoryName}</span>
              </>
            ) : (
              <span style={{ cursor: 'grab' }}>{categoryName}</span>
            )}
          </>
        ) : (
          <>
            <Icon
              style={{ marginRight: '5px' }}
              className={trashIconStyle.trashIcon}
              name="trash-alt"
              onClick={removeItem}
            />
            <div style={{ lineHeight: '0.9', justifyContent: 'center', cursor: 'pointer' }}>
              <div style={{ fontSize: '10px', display: index > 0 ? '' : 'none' }} onClick={moveUp}>
                &#x25B2;
              </div>
              <div style={{ fontSize: '10px', display: index < quantity - 1 ? '' : 'none' }} onClick={moveDown}>
                &#x25BC;
              </div>
            </div>
            <span style={{ marginLeft: '10px', cursor: 'grab' }}>{categoryName}</span>
          </>
        )}
      </button>
    );
  }
);

CatItem.displayName = 'CatItem';
