import { LoadingState, PanelProps, getFieldDisplayName } from '@grafana/data';
import { Icon, IconName, useTheme } from '@grafana/ui';
import { parseData } from 'CrossTabCtrl';
import React from 'react';
import { LIMITS, Options } from 'types';
import { PivotTable } from './pivotTable/TableRenderers';
import { find } from 'lodash';

export const notificationStyle = {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  height: '100%',
};

export interface CrossTabPanelProps extends PanelProps<Options> {}

export const CrossTabPanel: React.FC<CrossTabPanelProps> = React.memo(
  ({ data, width, height, options, fieldConfig, replaceVariables, onOptionsChange }) => {
    if (!data) {
      return (
        <div className="panel-empty">
          <p>No data found in response</p>
        </div>
      );
    }
    if (data.state === LoadingState.Loading) {
      return (
        <div style={notificationStyle}>
          <p>Loading...</p>
        </div>
      );
    }
    if (options.colMax && options.rowMax && options.colMax * options.rowMax > LIMITS.VIEW_MODE_NODE_LIMIT) {
      return (
        <div style={notificationStyle}>
          {renderAlertNotification(
            `The number of selected rows and columns exceeds the set limit. Ensure that the product of the rows and columns is less than ${LIMITS.VIEW_MODE_NODE_LIMIT}`,
            'error'
          )}
        </div>
      );
    }
    let pivotData: any;
    if (data.error || data.series.length === 0) {
      // eslint-disable-next-line react-hooks/rules-of-hooks
      const textColor = useTheme().colors.text;
      return (
        <div style={notificationStyle}>
          <span style={{ color: textColor }}>No data</span>
        </div>
      );
    } else {
      try {
        checkCategoriesValues(options);
        pivotData = parseData(data, options);
        // for now testing it for view mode
        //if(locationService.getSearchObject().editPanel === undefined) {
        updateCategoriesValueAccordingToData(options, data);
        //}
      } catch {
        return (
          <div style={notificationStyle}>
            {renderAlertNotification('Failed to parse plugin response data', 'error')}
          </div>
        );
      }
    }

    return (
      <PivotTable
        series={data.series[0]}
        width={width}
        height={height}
        data={pivotData}
        categories={options.categories}
        aggregatorType={options.aggregatorType}
        fieldConfig={fieldConfig}
        replaceVariables={replaceVariables}
        showTotals={options.showTotals}
        showSubTotals={options.showSubTotals}
        subTotalColumn={options.subTotalColumn}
        showRowPercentage={options.showRowPercentage}
        showColumnPercentage={options.showColumnPercentage}
        hideEmpty={options.hideEmpty}
        onChange={onOptionsChange}
        rowMax={options.rowMax}
        colMax={options.colMax}
        colorOptions={{
          headerColor: options.headerColor,
          headerBgColor: options.headerBgColor,
          subTotalHeaderBgColor: options.subTotalHeaderBgColor,
        }}
        textAlign={{
          columnTextAlign: options.columnTextAlign ?? 'left',
          rowTextAlign: options.rowTextAlign ?? 'left',
          valueTextAlign: options.valueTextAlign ?? 'right',
        }}
      />
    );
  }
);

CrossTabPanel.displayName = 'CrossTabPanel';

const renderAlertNotification = (msg: string, severity: string) => {
  return (
    <div className={'alert alert-' + severity}>
      <div className="alert-icon">
        <Icon size="xl" name={'exclamation-triangle' as IconName} />
      </div>
      <div className="alert-body">
        <div className="alert-title">{msg}</div>
      </div>
    </div>
  );
};

// this works during editor mode when we can change options
const checkCategoriesValues = (options: Options) => {
  let cols = updateCategories(options.categories.columnsCategories);
  options.categories.columnsCategories = cols;
  let rows = updateCategories(options.categories.rowsCategories);
  options.categories.rowsCategories = rows;
  let value = updateCategories(options.categories.valueCategory);
  options.categories.valueCategory = value;
};

const updateCategories = (data: any) => {
  const index = data.findIndex((value: any) => {
    return typeof value === 'object' && value != null;
  });
  if (index === -1) {
    const categories: any[] = [];
    data.forEach((item: any) => {
      item !== null && item !== undefined && categories.push({ name: item, displayName: item });
    });
    return categories;
  }
  return data;
};

// update categories object during viewing mode
// editing mode update is taken care by pivotConfigEditor
const updateCategoriesValueAccordingToData = (options: Options, data: any) => {
  // get new categories as per data changed
  const newColumns = getCategoriesOptions(data.series);
  options.categories.categoriesValues = getCategoriesValues(data.series, newColumns);
};

/**
 * Returns options list for pivot table categories
 */
const getCategoriesOptions = (data: any) => {
  const categories: any[] = [];
  if (data && data.length > 0) {
    data[0].fields.forEach((item: any) => {
      categories.push({ name: item.name, displayName: getFieldDisplayName(item, data[0]) });
    });
  }
  return categories;
};

const getCategoriesValues = (data: any, categories: any[]) => {
  const categoriesValues: any = {};
  if (data && data.length > 0) {
    data[0].fields.forEach((item: any) => {
      const catItem = find(categories, ['name', item.name]);
      if (catItem) {
        categoriesValues[item.name] = {
          values: [],
          isShow: false,
        };
        item.values.toArray().map((val: string) => {
          if (!categoriesValues[item.name].values.includes(val)) {
            categoriesValues[item.name].values.push(val);
          }
        });
      }
    });
  }
  return categoriesValues;
};
