import _ from 'lodash';
import { useTheme, useTheme2, Icon, Popover } from '@grafana/ui';
import {
  getDisplayProcessor,
  formattedValueToString,
  DataFrame,
  GrafanaTheme2,
  ValueMappingResult,
  RangeMapOptions,
  RegexMapOptions,
  SpecialValueOptions,
  LinkModel,
  DataLink,
} from '@grafana/data';
import { enhanceDataLinks, isDecimalAllowed } from 'CrossTabCtrl';
import { cx } from '@emotion/css';
import React, { CSSProperties, useState } from 'react';
import { colorScaleGenerator, getFieldStyle, getTableStyles } from '../styles';
import { CellDisplayMode, PivotTableProps, SUB_TOTAL_KEY } from '../types';
import { PivotData, addSeparators } from './pivotUtils';
import { RunTimeFilter, updateRunTimeFilter } from './runTimeFilter';

// helper function for setting row/col-span in pivotTableRenderer
const spanSize = function (arr: any, i: any, j: any) {
  let x: any;
  let len = 0;
  let noDraw = i !== 0 ? true : false;
  while (i + len < arr.length) {
    let asc1: any, end1: any;
    let stop = false;
    for (x = 0, end1 = j, asc1 = end1 >= 0; asc1 ? x <= end1 : x >= end1; asc1 ? x++ : x--) {
      if (noDraw && arr[i - 1][x] !== arr[i][x]) {
        noDraw = false;
      }
      if (arr[i][x] !== arr[i + len][x]) {
        stop = true;
      }
    }
    if (noDraw) {
      return -1;
    }
    if (stop) {
      break;
    }
    len++;
  }
  return len;
};

const getDisplayValue = ({ unit, decimals, aggregatorType, theme }, val) => {
  const dec = isDecimalAllowed(aggregatorType) ? decimals : 0;
  const displayProcessor = getDisplayProcessor({
    field: {
      config: {
        unit: unit || 'string',
        decimals: dec,
      },
    },
    theme,
  });
  const displayValueObj = displayProcessor(('' + val).replace(/,/g, ''));
  const displayVal = displayValueObj && displayValueObj.text !== 'NaN' ? formattedValueToString(displayValueObj) : val;
  const displayNum: any = ('' + displayVal).replace(/,/g, '');
  return !isNaN(displayNum) ? addSeparators((1 * displayNum).toFixed(dec), ',', '.') : displayVal;
};

const getColorValue = (
  options: Record<string, ValueMappingResult> | RangeMapOptions | RegexMapOptions | SpecialValueOptions | undefined,
  val: string,
  theme: GrafanaTheme2
): string => {
  let color = '';
  if (!options) {
    return color;
  }
  if (options[val]?.color) {
    return theme.visualization.getColorByName(options[val].color);
  }
  for (const k in options) {
    const c = options[k].text === val ? options[k]?.color : undefined;
    if (c) {
      return theme.visualization.getColorByName(c);
    }
  }
  return color;
};

const getDataLinkForHeaders = (
  val: string,
  series: DataFrame,
  dataLinks: DataLink[],
  fieldName?: string
): LinkModel | undefined => {
  if (!fieldName) {
    return undefined;
  }
  const fieldInContext = series.fields.find((i) => i.name === fieldName);
  if (!_.isEqual(dataLinks, fieldInContext?.config.links)) {
    const link = fieldInContext?.getLinks?.({
      calculatedValue: {
        text: val.replace(/[\u{0080}-\u{FFFF}]/gu, ' '),
        numeric: fieldInContext.type === 'time' ? +new Date(val) : Number(val),
      },
    })[0];
    return link;
  }
  return undefined;
};
const getFormattedVal = (
  val: string,
  series: DataFrame,
  theme: GrafanaTheme2,
  fieldName?: string
): { val: string; cssProps: CSSProperties } => {
  if (!fieldName) {
    return { val, cssProps: {} };
  }
  const fieldInContext = series.fields.find((i) => i.name === fieldName);
  if (!fieldInContext?.display) {
    return { val, cssProps: {} };
  }
  const cssProps: CSSProperties = {};
  const fColor = getColorValue(fieldInContext?.config?.mappings?.[0]?.options, val, theme);
  if (fColor) {
    switch (fieldInContext?.config?.custom?.cellDisplayMode) {
      case CellDisplayMode.ColorText:
        cssProps.color = fColor;
        break;
      case CellDisplayMode.ColorBackground:
        cssProps.backgroundColor = fColor;
        break;
      default:
        break;
    }
  }
  return { val, cssProps };
};

export const PivotTable: React.FC<PivotTableProps> = React.memo(
  ({
    data,
    width,
    height,
    categories,
    aggregatorType,
    fieldConfig,
    showTotals,
    showSubTotals,
    subTotalColumn,
    replaceVariables,
    showRowPercentage,
    showColumnPercentage,
    hideEmpty,
    onChange,
    rowMax,
    colMax,
    colorOptions,
    series,
    textAlign,
  }) => {
    const theme = useTheme();
    const theme2 = useTheme2();
    let pivotProp: any = {
      width: width,
      height: height,
      data: data,
      rows: categories.rowsCategories,
      cols: categories.columnsCategories,
      vals: categories.valueCategory,
      rowOrder: categories.rowOrder,
      colOrder: categories.colOrder,
      valueFilter: categories.categoriesValueFilter,
      aggregatorType: aggregatorType,
      showTotals: showTotals,
      showSubTotals: showSubTotals,
      subTotalColumn: subTotalColumn,
      showRowPercentage: showRowPercentage,
      showColumnPercentage: showColumnPercentage,
      hideEmpty: hideEmpty,
      fieldConfig: fieldConfig,
      theme: theme,
      rowMax,
      colMax,
      headerColor: colorOptions.headerColor,
      headerBgColor: colorOptions.headerBgColor,
      subTotalHeaderBgColor: colorOptions.subTotalHeaderBgColor,
    };
    const pivotData = new PivotData(pivotProp);
    pivotProp = pivotData.props;
    const colAttrs = pivotProp.cols;
    const rowAttrs = pivotProp.rows;
    const rowKeys = pivotData.getRowKeys();
    const colKeys = pivotData.getColKeys();
    const { colsHidden, rowsHidden } = pivotData.getHideStatus();
    const grandTotalAggregator = pivotData.getAggregator([], []);
    const tableContainerWidth = pivotProp.width;
    const tableContainerHeight = pivotProp.height;
    const cellDisplayMode = fieldConfig.defaults.custom.cellDisplayMode;
    const thresholds = fieldConfig.defaults.thresholds;
    const dataLinks = fieldConfig.defaults.links ?? [];
    const filterable = categories.filterable;
    const subTotalColumnIndex = pivotProp.subTotalColumn;

    // deleting /resetting unwanted memory
    delete pivotData.aggregator;

    const styles = getTableStyles(theme, colorOptions);
    const getFieldStyles = (field: 'column' | 'row' | 'value', isSubTotalRow?: boolean) =>
      getFieldStyle(field, theme2, {
        column: {
          textAlign: textAlign.columnTextAlign,
          color: colorOptions.headerColor,
          background: colorOptions.headerBgColor,
        },
        row: {
          textAlign: textAlign.rowTextAlign,
          color: colorOptions.headerColor,
          background: isSubTotalRow ? colorOptions.subTotalHeaderBgColor : colorOptions.headerBgColor,
        },
        value: {
          textAlign: textAlign.valueTextAlign,
        },
      });

    if (
      aggregatorType.includes('Sum as') ||
      aggregatorType.includes('Count as') ||
      categories.valueCategory.length > 1
    ) {
      showRowPercentage = false;
      showColumnPercentage = false;
    }

    const [isColPopoverVisible, setColPopoverVisible] = useState({});
    const [isRowPopoverVisible, setRowPopoverVisible] = useState({});
    /* istanbul ignore next*/
    const onShowColPopover = (index: any) => (event: any) => {
      setColPopoverVisible({ ...isColPopoverVisible, [index]: true });
    };
    const onCloseColPopover = (index: any) => {
      setColPopoverVisible({ ...isColPopoverVisible, [index]: false });
    };

    const onShowRowPopover = (index: any) => (event: any) => {
      setRowPopoverVisible({ ...isRowPopoverVisible, [index]: true });
    };
    const onCloseRowPopover = (index: any) => {
      setRowPopoverVisible({ ...isRowPopoverVisible, [index]: false });
    };

    const getDisplayValueWrapper = React.useCallback(
      (val) => {
        return getDisplayValue(
          { unit: fieldConfig?.defaults.unit, decimals: fieldConfig?.defaults.decimals, aggregatorType, theme: theme2 },
          val
        );
      },
      // eslint-disable-next-line react-hooks/exhaustive-deps
      [fieldConfig, aggregatorType]
    );

    let spanSizeCounter = 0;
    return (
      <div style={{ width: tableContainerWidth, height: tableContainerHeight, overflow: 'auto' }}>
        <table className={styles.container}>
          <thead>
            {colAttrs.map(function (c: any, j: any) {
              const colEl = Array.from(document.querySelectorAll('th')).find(
                (el) => el.textContent === c.displayName
              ) as HTMLElement;
              const isFilterVisible = filterable[c.name] ? filterable[c.name].isShow : false;
              // @ts-ignore
              return (
                <tr key={`colAttr${j}`} style={{ ...getFieldStyles('column') }}>
                  {j === 0 && rowAttrs.length !== 0 && (
                    <th className={styles.headersCol} colSpan={rowAttrs.length} rowSpan={colAttrs.length} />
                  )}
                  <th className={styles.headersCol}>
                    {c.displayName}
                    {isFilterVisible && (
                      <div className={styles.filterItem} onClick={onShowColPopover(j)}>
                        <Icon name={'filter'} />
                        {isColPopoverVisible[j] && (
                          <Popover
                            content={
                              <RunTimeFilter
                                attr={c}
                                cat={categories}
                                onClose={() => onCloseColPopover(j)}
                                onChange={(cat: any, val: string, checked: boolean) =>
                                  updateRunTimeFilter(cat, val, checked, pivotProp, categories, onChange)
                                }
                              />
                            }
                            placement="bottom-start"
                            referenceElement={colEl}
                            show
                          />
                        )}
                      </div>
                    )}
                  </th>
                  {colKeys.map(function (colKey: any, i: any) {
                    const x = spanSize(colKeys, i, j);
                    const totalColumnAggregator = pivotData.getAggregator([], colKey);
                    if (x === -1) {
                      return null;
                    }
                    const formattedVal = getFormattedVal(colKey[j], series, theme2, colAttrs[j]?.name);
                    const link = getDataLinkForHeaders(formattedVal.val, series, dataLinks, colAttrs[j]?.name);
                    if (totalColumnAggregator.value || !hideEmpty) {
                      return (
                        <th
                          style={formattedVal.cssProps}
                          className={cx(styles.colLabel, styles.headersCol)}
                          key={`colKey${i}`}
                          colSpan={x}
                          rowSpan={j === colAttrs.length - 1 && rowAttrs.length !== 0 ? 2 : 1}
                        >
                          {link ? (
                            <a
                              className={cx(styles.headers, styles.linkValues)}
                              style={{ ...formattedVal.cssProps, border: 0, cursor: 'pointer' }}
                              href={link.href}
                              title={link.title}
                              target={link.target}
                            >
                              {formattedVal.val}
                            </a>
                          ) : (
                            formattedVal.val
                          )}
                        </th>
                      );
                    } else {
                      return true;
                    }
                  })}
                  {j === 0 && showTotals && (
                    <>
                      {colsHidden && (
                        <td
                          className={cx(styles.totalLabel, styles.headersCol)}
                          rowSpan={colAttrs.length + (rowAttrs.length === 0 ? 0 : 1)}
                        ></td>
                      )}
                      <th className={cx(styles.headersCol)} rowSpan={colAttrs.length + (rowAttrs.length === 0 ? 0 : 1)}>
                        Total
                      </th>
                    </>
                  )}
                </tr>
              );
            })}
            {rowAttrs.length !== 0 && (
              <tr style={{ ...getFieldStyles('column') }}>
                {rowAttrs.map(function (r: any, i: any) {
                  const rowEl = Array.from(document.querySelectorAll('th')).find(
                    (el) => el.textContent === r.displayName
                  ) as HTMLElement;
                  const isFilterVisible = filterable[r.name] ? filterable[r.name].isShow : false;
                  // @ts-ignore
                  // @ts-ignore
                  return (
                    <th className={styles.headersCol} key={`rowAttr${i}`}>
                      {r.displayName}
                      {isFilterVisible && (
                        <div className={styles.filterItem} onClick={onShowRowPopover(i)}>
                          <Icon name={'filter'} />
                          {isRowPopoverVisible[i] && (
                            <Popover
                              content={
                                <RunTimeFilter
                                  attr={r}
                                  cat={categories}
                                  onClose={() => onCloseRowPopover(i)}
                                  onChange={(cat: any, val: string, checked: boolean) =>
                                    updateRunTimeFilter(cat, val, checked, pivotProp, categories, onChange)
                                  }
                                />
                              }
                              placement="bottom-start"
                              referenceElement={rowEl}
                              show
                            />
                          )}
                        </div>
                      )}
                    </th>
                  );
                })}
                {showTotals && colAttrs.length === 0 && (
                  <>
                    {colsHidden && <td className={cx(styles.totalLabel, styles.headers)}></td>}
                    <th className={cx(styles.totalLabel, styles.headers)}>{'Total'}</th>
                  </>
                )}
                {showTotals && colAttrs.length > 0 && <th className={cx(styles.totalLabel, styles.headers)}>{null}</th>}
                {!showTotals && colAttrs.length > 0 && (
                  <th className={cx(styles.totalLabel, styles.headers)}>{null}</th>
                )}
                {showTotals && !colAttrs.length && ''}
              </tr>
            )}
          </thead>
          <tbody>
            {rowKeys.map((rowKey: any[], i: any) => {
              const totalAggregator = pivotData.getAggregator(rowKey, []);
              const totalAggVal = totalAggregator.value;
              if (spanSizeCounter === 0 && i === 0) {
                spanSizeCounter = spanSize(rowKeys, i, subTotalColumnIndex + 1);
              }
              if (totalAggVal || !hideEmpty) {
                spanSizeCounter--;
                return (
                  <tr key={`rowKeyRow${i}`}>
                    {rowKey.map(function (txt: any, j: any) {
                      const x = spanSize(rowKeys, i, j);
                      const subTotalIndex = rowKey.indexOf(SUB_TOTAL_KEY);
                      let subTotalColSpan = 1;
                      if (txt === SUB_TOTAL_KEY && subTotalIndex > -1 && subTotalIndex < rowAttrs.length - 1) {
                        subTotalColSpan = rowAttrs.length - subTotalIndex;
                      }
                      if (x === -1 && subTotalColSpan === 1) {
                        return null;
                      }
                      const formattedVal = getFormattedVal(txt, series, theme2, rowAttrs[j]?.name);
                      const link = getDataLinkForHeaders(formattedVal.val, series, dataLinks, rowAttrs[j]?.name);
                      const colSpan = j === rowAttrs.length - 1 && colAttrs.length !== 0 ? 2 : 1;
                      return (
                        <th
                          key={`rowKeyLabel${i}-${j}`}
                          className={cx(styles.headers)}
                          style={{
                            ...formattedVal.cssProps,
                            ...getFieldStyles('row', subTotalIndex > -1 ? true : false),
                          }}
                          rowSpan={x}
                          colSpan={subTotalColSpan > 1 ? subTotalColSpan + colSpan : colSpan}
                        >
                          {link ? (
                            <a
                              className={cx(styles.headers, styles.linkValues)}
                              style={{ ...formattedVal.cssProps, border: 0, cursor: 'pointer' }}
                              href={link.href}
                              title={link.title}
                              target={link.target}
                            >
                              {formattedVal.val}
                            </a>
                          ) : (
                            formattedVal.val
                          )}
                        </th>
                      );
                    })}
                    {colKeys.map(function (colKey: any, j: any) {
                      const aggregator = pivotData.getAggregator(rowKey, colKey);
                      const value = aggregator.value;
                      const formattedValue = getDisplayValueWrapper(aggregator.format);
                      const totalColumnAggregatorVal = pivotData.getAggregator([], colKey).value;
                      const showRowPerc =
                        showTotals && showRowPercentage ? '(' + Math.round((value / totalAggVal) * 100) + '%)' : '';
                      const showColPerc =
                        showTotals && showColumnPercentage
                          ? '(' + Math.round((value / totalColumnAggregatorVal) * 100) + '%)'
                          : '';
                      const enhancedLink =
                        value && value !== 0 && dataLinks && dataLinks.length > 0
                          ? enhanceDataLinks(rowAttrs, rowKey, colAttrs, colKey, dataLinks[0], replaceVariables)
                          : null;
                      const cellColor: CSSProperties = colorScaleGenerator(theme, cellDisplayMode, thresholds, value);
                      if (totalColumnAggregatorVal || !hideEmpty) {
                        return (
                          <td
                            className={cx(styles.values)}
                            style={{ ...cellColor, ...getFieldStyles('value') }}
                            key={`pvtVal${i}-${j}`}
                          >
                            {enhancedLink ? (
                              <div className={cx(styles.linkValues)}>
                                <a
                                  style={cellColor}
                                  href={enhancedLink.url}
                                  title={enhancedLink.title}
                                  rel="noreferrer"
                                  target={enhancedLink.targetBlank ? '_blank' : undefined}
                                >
                                  {showTotals ? `${formattedValue} ${showRowPerc} ${showColPerc}` : formattedValue}
                                </a>
                              </div>
                            ) : showTotals ? (
                              `${formattedValue} ${showRowPerc} ${showColPerc}`
                            ) : (
                              formattedValue
                            )}
                          </td>
                        );
                      } else {
                        return true;
                      }
                    })}
                    {showTotals && (
                      <>
                        {colsHidden && <td className={cx(styles.values)}>...</td>}
                        <td style={{ ...getFieldStyles('value') }} className={cx(styles.values, styles.totalValue)}>
                          {getDisplayValueWrapper(totalAggregator.format)}
                        </td>
                      </>
                    )}
                  </tr>
                );
              } else {
                return true;
              }
            })}
            {showTotals && (
              <>
                {rowsHidden && (
                  <tr>
                    <th
                      className={cx(styles.totalLabel, styles.headers)}
                      colSpan={rowAttrs.length + (colAttrs.length === 0 ? 0 : 1)}
                    ></th>
                    {colKeys.map(function (colKey: any, i: any) {
                      return (
                        <td className={cx(styles.values)} key={`total${i}`}>
                          ...
                        </td>
                      );
                    })}
                    {colsHidden && <td className={cx(styles.values)}>...</td>}
                    <td className={cx(styles.values)}></td>
                  </tr>
                )}
                <tr>
                  <th
                    style={{ ...getFieldStyles('row') }}
                    className={cx(styles.totalLabel, styles.headers)}
                    colSpan={rowAttrs.length + (colAttrs.length === 0 ? 0 : 1)}
                  >
                    Total
                  </th>
                  {colKeys.map(function (colKey: any, i: any) {
                    const totalAggregator = pivotData.getAggregator([], colKey);
                    if (totalAggregator.value || !hideEmpty) {
                      return (
                        <td
                          style={{ ...getFieldStyles('value'), color: theme2.colors.text }}
                          className={cx(styles.values, styles.totalValue)}
                          key={`total${i}`}
                        >
                          {getDisplayValueWrapper(totalAggregator.format)}
                        </td>
                      );
                    } else {
                      return true;
                    }
                  })}
                  {colsHidden && <td className={cx(styles.values)}></td>}
                  <td
                    style={{ ...getFieldStyles('value'), color: theme2.colors.text }}
                    className={cx(styles.values, styles.totalValue)}
                  >
                    {getDisplayValueWrapper(grandTotalAggregator.format)}
                  </td>
                </tr>
              </>
            )}
          </tbody>
        </table>
      </div>
    );
  }
);

PivotTable.displayName = 'PivotTable';
