import React, { useCallback } from 'react';
import { Checkbox, ClickOutsideWrapper, stylesFactory, useStyles, VerticalGroup } from '@grafana/ui';
import { GrafanaTheme } from '@grafana/data';
import { css, cx } from '@emotion/css';

interface Props {
  attr: any;
  cat: any;
  onClose: () => void;
  onChange: (cat: string, val: any, selected: boolean) => void;
}

export const RunTimeFilter: React.FC<Props> = ({ attr, cat, onClose, onChange }) => {
  const onCancel = useCallback((event?: React.MouseEvent) => onClose(), [onClose]);
  if (!cat.categoriesValueFilter[attr.name]) {
    cat.categoriesValueFilter[attr.name] = [];
  }

  const renderFilterItemValue = (val: string) => {
    /* istanbul ignore next */
    return (
      <div key={val}>
        <Checkbox
          value={!cat.categoriesValueFilter[attr.name].includes(val)}
          label={val}
          onChange={(e) => onToggleChecked(e, val)}
        />
      </div>
    );
  };

  const onToggleChecked = (e: any, val: string) => {
    /* istanbul ignore next */
    onChange(attr.name, val, e.currentTarget.checked);
  };
  const getStyles = stylesFactory((theme: GrafanaTheme) => ({
    filterContainer: css`
      label: filterContainer;
      width: 100%;
      min-width: 250px;
      height: 100%;
      max-height: 400px;
      overflow: auto;
      background-color: ${theme.colors.bg1};
      border: ${theme.border.width.sm} solid ${theme.colors.border2};
      padding: ${theme.spacing.md};
      margin: ${theme.spacing.sm} 0;
      box-shadow: 0px 0px 20px ${theme.colors.dropdownShadow};
      border-radius: ${theme.spacing.xs};
    `,
  }));

  const styles = useStyles(getStyles);

  return (
    <ClickOutsideWrapper onClick={onCancel} useCapture={true}>
      <div className={cx(styles.filterContainer)} onClick={stopPropagation}>
        <VerticalGroup spacing="xs">
          <div>{cat.categoriesValues[attr.name].values.map((key: any) => renderFilterItemValue(key))}</div>
        </VerticalGroup>
      </div>
    </ClickOutsideWrapper>
  );
};

export const updateRunTimeFilter = (
  cat: string,
  val: string,
  selected: boolean,
  options: any,
  categories: any,
  onChange: (options: any) => void
) => {
  if (!options.valueFilter[cat]) {
    options.valueFilter[cat] = [];
  }
  if (!options.categories) {
    options.categories = categories;
  }
  const index = options.valueFilter[cat].indexOf(val);
  if (!selected) {
    if (index === -1) {
      options.valueFilter[cat].push(val);
    }
  } else {
    if (index !== -1) {
      options.valueFilter[cat].splice(index, 1);
    }
  }
  // deleting options.data so that it doesn't get saved in json model.
  if (options.data) {
    delete options.data;
  }
  onChange(options);
};

const stopPropagation = (event: React.MouseEvent) => {
  /* istanbul ignore next */
  event.stopPropagation();
};
