import { useTheme } from '@grafana/ui';
import { cx } from '@emotion/css';
import React from 'react';
import { getLabelStyles } from '../styles';
import { CategoryItem, CategoryType } from './PivotCategoriesEditor';
import { PivotCategoryItem } from './PivotCategoryItem';
import { clone as _clone, find as _find } from 'lodash';

interface PivotCategoriesPickerProps {
  categories: any[];
  filterQuery: string;
  rowCategories: any[];
  columnCategories: any[];
  valCategories: any[];
  onSelect: (categories: CategoryItem[]) => void;
  onChangeOrder: (catByTypes: any) => void;
}

export const PivotCategoriesPicker: React.FC<PivotCategoriesPickerProps> = ({
  categories,
  filterQuery,
  rowCategories,
  columnCategories,
  valCategories,
  onSelect,
  onChangeOrder,
}) => {
  const theme = useTheme();
  const labelStyles = getLabelStyles(theme);

  let allCategories: any[] = [];
  allCategories = createCategoriesObject(categories, filterQuery, rowCategories, columnCategories, valCategories);

  let catByTypes: any = {
    row: [],
    col: [],
    val: [],
    none: [],
  };

  allCategories.forEach((t) => {
    const catType = t.type;
    let index = 0;
    let quantity = 0;
    if (catType === CategoryType.COLUMN) {
      index = columnCategories.indexOf(t.name);
      quantity = columnCategories.length;
    } else if (catType === CategoryType.ROW) {
      index = rowCategories.indexOf(t.name);
      quantity = rowCategories.length;
    }
    /* istanbul ignore next */
    catByTypes[catType].push(
      <PivotCategoryItem
        key={t.name}
        categoryName={t.name}
        catType={catType}
        index={index}
        quantity={quantity}
        onDragStart={(e: any) => onDragStart(e, t.name)}
        moveUp={() => moveUp(t, catByTypes, onChangeOrder)}
        moveDown={() => moveDown(t, catByTypes, onChangeOrder)}
        removeItem={() => removeItem(t, allCategories, onSelect)}
      />
    );
  });

  /* istanbul ignore next */
  return (
    <>
      <div
        id="not-selected-cat-area"
        style={{ display: 'flex', padding: '5px 28px 0px 0px', flexWrap: 'wrap' }}
        onDragOver={(e) => onDragOver(e)}
        onDrop={(e) => {
          onDrop(e, CategoryType.NONE, allCategories, onSelect);
        }}
      >
        {catByTypes[CategoryType.NONE]}
      </div>

      <label className={cx(labelStyles.description)} style={{ marginTop: '20px' }}>
        {'Column fields:'}
      </label>
      <div
        id="column-cat-area"
        style={{ minHeight: '40px', maxHeight: '140px', overflowY: 'auto' }}
        onDragOver={(e) => onDragOver(e)}
        onDrop={(e) => {
          onDrop(e, CategoryType.COLUMN, allCategories, onSelect);
        }}
      >
        {catByTypes[CategoryType.COLUMN].length > 0 && catByTypes[CategoryType.COLUMN]}
      </div>

      <label className={cx(labelStyles.description)} style={{ marginTop: '20px' }}>
        {'Row fields:'}
      </label>
      <div
        id="row-cat-area"
        style={{ minHeight: '40px', maxHeight: '140px', overflowY: 'auto' }}
        onDragOver={(e) => onDragOver(e)}
        onDrop={(e) => {
          onDrop(e, CategoryType.ROW, allCategories, onSelect);
        }}
      >
        {catByTypes[CategoryType.ROW].length > 0 && catByTypes[CategoryType.ROW]}
      </div>

      <label className={cx(labelStyles.description)} style={{ marginTop: '20px' }}>
        {'Value field:'}
      </label>
      <div
        id="value-cat-area"
        style={{ height: '40px', maxHeight: '140px', overflowY: 'auto' }}
        onDragOver={(e) => onDragOver(e)}
        onDrop={(e) => onDrop(e, CategoryType.VALUE, allCategories, onSelect)}
      >
        {catByTypes[CategoryType.VALUE].length > 0 && catByTypes[CategoryType.VALUE]}
      </div>
    </>
  );
};

/**
 * Returns categories list for select.
 * Remove selected (columns, rows, and value) categories and filter by search query.
 */
const createCategoriesObject = (
  categories: any[],
  filterQuery: string,
  rowCategories: any[],
  columnCategories: any[],
  valCategories: any[]
) => {
  let catObject: CategoryItem[] = [];
  const selectedCategories: any[] = [...rowCategories, ...columnCategories, ...valCategories];
  const unselectedCategories: any[] = categories.filter((cat) => !selectedCategories.includes(cat));
  const filteredCategories: any[] = unselectedCategories.filter((cat) =>
    cat.name.toLowerCase().includes(filterQuery.toLowerCase())
  );
  rowCategories.forEach((cat) => {
    cat && catObject.push({ name: cat.name, type: CategoryType.ROW });
  });
  columnCategories.forEach((cat) => {
    cat && catObject.push({ name: cat.name, type: CategoryType.COLUMN });
  });
  valCategories.forEach((cat) => {
    cat && catObject.push({ name: cat.name, type: CategoryType.VALUE });
  });
  filteredCategories.forEach((cat) => {
    cat && catObject.push({ name: cat.name, type: CategoryType.NONE });
  });
  return catObject;
};

/* istanbul ignore next */
const onDragStart = (ev: any, id: string) => {
  ev.dataTransfer.setData('id', id);
  ev.dataTransfer?.setData('dragFrom', (ev.target as any).parentElement?.id);
};

/* istanbul ignore next */
const onDragOver = (ev: any) => {
  ev.preventDefault();
};

/**
 * Update category position (move up) in categories list (row and column categories).
 */
export const moveUp = (category: CategoryItem, catByTypes: any, onChangeOrder: (catByTypes: any) => void) => {
  moveArrayItemToNewIndex(category, true, catByTypes, onChangeOrder);
};

/**
 * Update category position (move down) in categories list (row and column categories).
 */
export const moveDown = (category: CategoryItem, catByTypes: any, onChangeOrder: (catByTypes: any) => void) => {
  moveArrayItemToNewIndex(category, false, catByTypes, onChangeOrder);
};

/**
 * Set/Update category type in category object after drop.
 */
export const onDrop = (ev: any, type: CategoryType, allCategories: any, onSelect: (categories: any) => void) => {
  if (ev.dataTransfer?.getData('dragFrom') === ev.currentTarget?.id) {
    return ev.preventDefault?.();
  }
  const id = ev.dataTransfer?.getData('id');
  const filteredCategories: any[] = [];
  allCategories.forEach((cat: any) => {
    const itemClone = _clone(cat);
    if (itemClone.name === id) {
      itemClone.type = type;
    }
    !_find(filteredCategories, (el: any) => el.name === itemClone.name && el.type === itemClone.type) &&
      filteredCategories.push(itemClone);
  });
  onSelect(filteredCategories);
};

/**
 * Update category position in categories list (row and column categories).
 */
const moveArrayItemToNewIndex = (
  category: CategoryItem,
  up: boolean,
  catByTypes: any,
  onChangeOrder: (catByTypes: any) => void
) => {
  const arr = catByTypes[category.type];
  const current_index = arr.findIndex((item: any) => item.key === category.name);
  const new_index = up ? current_index - 1 : current_index + 1;
  arr.splice(new_index, 0, arr.splice(current_index, 1)[0]);
  catByTypes[category.type] = [...arr];
  onChangeOrder(catByTypes);
};

/**
 * Remove category type (set to 'none) from category object.
 */
export const removeItem = (category: CategoryItem, allCategories: any, onSelect: (categories: any) => void) => {
  let filteredCategories = allCategories.filter((cat: { name: string; type: CategoryType }) => {
    if (cat.name === category.name) {
      cat.type = CategoryType.NONE;
    }
    return cat;
  });
  onSelect(filteredCategories);
};
