import { GrafanaTheme, GrafanaTheme2 } from '@grafana/data';
import { useTheme2 } from '@grafana/ui';
import { StyleProps } from 'customPanelEditors/PivotCategoryItem';
import { css, cx } from '@emotion/css';
import { CSSProperties } from 'react';
import tinycolor from 'tinycolor2';
import { CellDisplayMode, ColorOptions, TextAlignment } from './types';

/*
 * Plugin Pivot categories settings editor styles
 */

/* istanbul ignore next */
export const getLabelStyles = (theme: GrafanaTheme) => {
  return {
    label: css`
      label: Label;
      font-size: ${theme.typography.size.sm};
      font-weight: ${theme.typography.weight.semibold};
      line-height: 1.25;
      margin: ${theme.spacing.formLabelMargin};
      padding: ${theme.spacing.formLabelPadding};
      color: ${theme.colors.formLabel};
      max-width: 480px;
    `,
    labelContent: css`
      display: flex;
      align-items: center;
    `,
    description: css`
      label: Label-description;
      color: ${theme.colors.formDescription};
      font-size: ${theme.typography.size.sm};
      font-weight: ${theme.typography.weight.regular};
      margin-top: ${theme.spacing.xxs};
      display: block;
    `,
  };
};

/* istanbul ignore next */
export const getTrashIconStyle = (theme: GrafanaTheme) => {
  return {
    trashIcon: css`
      color: ${theme.colors.textWeak};
      cursor: pointer;
      &:hover {
        color: ${theme.colors.text};
      }
    `,
  };
};

/* istanbul ignore next */
export const getButtonStyles = (props: StyleProps) => {
  const { theme } = props;
  const { padding, fontSize, height } = getPropertiesForButtonSize(props);
  const { background, borderColor } = getPropertiesForVariant(theme);

  return {
    button: cx(
      css`
        label: button;
        display: inline-flex;
        align-items: center;
        color: ${theme.colors.textBlue};
        font-weight: ${theme.typography.weight.semibold};
        font-family: ${theme.typography.fontFamily.sansSerif};
        font-size: ${fontSize};
        padding: ${padding};
        height: ${height}px;
        // Deduct border from line-height for perfect vertical centering on windows and linux
        line-height: ${height - 2}px;
        vertical-align: middle;
        cursor: default;
        border: 1px solid ${borderColor};
        border-radius: ${theme.border.radius.sm};
        ${background};

        &[disabled],
        &:disabled {
          cursor: not-allowed;
          opacity: 0.65;
          box-shadow: none;
        }
      `,
      getFocusStyle(theme)
    ),
  };
};

/* istanbul ignore next */
const getFocusStyle = (theme: GrafanaTheme) => css`
  &:focus {
    ${focusCss(theme)}
  }
`;

/* istanbul ignore next */
export const focusCss = (theme: GrafanaTheme) => `
  outline: 2px dotted transparent;
  outline-offset: 2px;
  box-shadow: 0 0 0 2px ${theme.colors.bodyBg}, 0 0 0px 4px ${theme.colors.formFocusOutline};
  transition: all 0.2s cubic-bezier(0.19, 1, 0.22, 1);
`;

/* istanbul ignore next */
const buttonVariantStyles = (from: string, to: string, textColor: string) => css`
  background: linear-gradient(180deg, ${from} 0%, ${to} 100%);
  color: ${textColor};
  &:hover {
    background: ${from};
    color: ${textColor};
  }

  &:focus {
    background: ${from};
    outline: none;
  }
`;

/* istanbul ignore next */
const getPropertiesForVariant = (theme: GrafanaTheme) => {
  const from = theme.isLight ? theme.palette.gray7 : theme.palette.gray15;
  const to = theme.isLight ? tinycolor(from).darken(5).toString() : tinycolor(from).lighten(4).toString();
  return {
    borderColor: theme.isLight ? theme.palette.gray85 : theme.palette.gray25,
    background: buttonVariantStyles(from, to, theme.isLight ? theme.palette.gray33 : theme.palette.gray85),
  };
};

/* istanbul ignore next */
const getPropertiesForButtonSize = (props: StyleProps) => {
  const { spacing, typography, height } = props.theme;
  return {
    padding: `0 ${spacing.sm}`,
    fontSize: typography.size.sm,
    height: height.sm,
  };
};

/* istanbul ignore next */
export const getCategoryFilterStyles = (theme: GrafanaTheme) => ({
  collapse: css`
    label: collapse;
    margin-top: ${theme.spacing.sm};
  `,
  collapseBody: css`
    label: collapse__body;
    padding: ${theme.spacing.xxs} ${theme.spacing.sm} ${theme.spacing.sm} ${theme.spacing.md};
    max-height: 150px;
    overflow-y: auto;
  `,
  header: css`
    label: collapse__header;
    padding: ${theme.spacing.sm} ${theme.spacing.md};
    display: flex;
    cursor: inherit;
    transition: all 0.1s linear;
    cursor: pointer;
  `,
  headerButtons: css`
    label: collapse__header-buttons;
    margin-right: ${theme.spacing.sm};
    margin-top: ${theme.spacing.xxs};
    font-size: ${theme.typography.size.lg};
    line-height: ${theme.typography.heading.h6};
    display: inherit;
  `,
  headerLabel: css`
    label: collapse__header-label;
    font-weight: ${theme.typography.weight.semibold};
    margin-right: ${theme.spacing.sm};
    font-size: ${theme.typography.heading.h6};
  `,
});

