/*
 * Pivot table styles
 */

// This is used to expose and add raw css on html tags to help renderer pick up crosstab styles
interface FieldStyle {
  color?: string;
  background?: string;
  textAlign?: TextAlignment;
}
export const getFieldStyle = (
  field: 'column' | 'row' | 'value',
  theme: GrafanaTheme2,
  style: { column: FieldStyle; row: FieldStyle; value: FieldStyle }
): any => {
  if (field === 'column') {
    return {
      ...(style.column.textAlign && { textAlign: style.column.textAlign, textAlignLast: style.column.textAlign }),
      ...(style.column.color && {
        color: style.column.color ? theme.visualization.getColorByName(style.column.color) : theme.v1.colors.textBlue,
      }),
      ...(style.column.background && {
        background: style.column.background
          ? theme.visualization.getColorByName(style.column.background)
          : theme.v1.colors.bg2,
      }),
    };
  }
  if (field === 'row') {
    return {
      ...(style.row.textAlign && { textAlign: style.row.textAlign }),
      ...(style.row.color && {
        color: style.row.color ? theme.visualization.getColorByName(style.row.color) : theme.v1.colors.textBlue,
      }),
      ...(style.row.background && {
        background: style.row.background
          ? theme.visualization.getColorByName(style.row.background)
          : theme.v1.colors.bg2,
      }),
    };
  }
  if (field === 'value') {
    return {
      ...(style.value.textAlign && { textAlign: style.value.textAlign }),
      ...(style.value.color && { color: style.value.color }),
      ...(style.value.background && { background: style.value.background }),
    };
  }
  return {};
};

