export const colorScaleGenerator = (
  theme: GrafanaTheme,
  cellDisplayMode: CellDisplayMode,
  thresholds: any,
  value: number
): CSSProperties => {
  let styles: CSSProperties = {};

  switch (cellDisplayMode) {
    case CellDisplayMode.None: {
      styles = {
        color: `${theme.colors.text}`,
      };
      break;
    }
    case CellDisplayMode.ColorText: {
      styles = {
        color: `${getColorByThreshold(thresholds, value, theme.colors.text, theme)}`,
      };
      break;
    }
    case CellDisplayMode.ColorBackground: {
      styles = {
        backgroundColor: `${getColorByThreshold(thresholds, value, '', theme)}`,
        color: `${theme.colors.text}`,
      };
      break;
    }
  }
  return styles;
};

const getColorByThreshold = (thresholds: any, value: number, defaultColor: string, theme: GrafanaTheme) => {
  let color: string = defaultColor;
  const thresholdMode = thresholds.mode;
  const cellValue = thresholdMode === 'absolute' ? value : value * 100;
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const theme2: GrafanaTheme2 = useTheme2();
  for (let entry of thresholds.steps) {
    if (cellValue >= entry.value) {
      color = theme2.visualization.getColorByName(entry.color);
    } else {
      return color;
    }
  }
  return color;
};
