import { Checkbox, Icon, useTheme } from '@grafana/ui';
import { cx } from '@emotion/css';
import React, { FunctionComponent, useState } from 'react';
import { getCategoryFilterStyles } from '../styles';

export interface FilterItemProps {
  category: string;
  values: string[];
  categoriesValueFilter: string[];
  onChange: (cat: string, val: string, status: boolean) => void;
}

export const FilterItem: FunctionComponent<FilterItemProps> = ({
  category,
  values,
  categoriesValueFilter,
  onChange,
}) => {
  const theme = useTheme();
  const style = getCategoryFilterStyles(theme);

  const [isOpen, setIsOpen] = useState(false);

  /* istanbul ignore next */
  const onClickToggle = () => {
    setIsOpen(!isOpen);
  };

  /* istanbul ignore next */
  const onToggleChecked = (e: any, val: string) => {
    onChange(category, val, e.currentTarget.checked);
  };

  /* istanbul ignore next */
  const renderFilterItemValue = (val: string) => {
    return (
      <div key={val}>
        <Checkbox value={!categoriesValueFilter.includes(val)} label={val} onChange={(e) => onToggleChecked(e, val)} />
      </div>
    );
  };
  /* istanbul ignore next */
  return (
    <div className={cx([style.collapse, 'panel-container'])}>
      <div className={style.header} onClick={onClickToggle}>
        <div className={style.headerButtons}>
          <Icon name={isOpen ? 'angle-up' : 'angle-down'} />
        </div>
        <div className={cx([style.headerLabel])}>{category}</div>
      </div>
      {isOpen && <div className={cx([style.collapseBody])}>{values.map((val) => renderFilterItemValue(val))}</div>}
    </div>
  );
};

FilterItem.displayName = 'FilterItem';
