import { DataFrame, InterpolateFunction } from '@grafana/data';

export type ColorOptions = { headerColor?: string; headerBgColor?: string; subTotalHeaderBgColor?: string };

export const SUB_TOTAL_KEY = 'Subtotal';

export type TextAlignment = 'left' | 'right' | 'center';
export type TextAlignOptions = {
  columnTextAlign: TextAlignment;
  rowTextAlign: TextAlignment;
  valueTextAlign: TextAlignment;
};

export interface PivotTableProps {
  series: DataFrame;
  width: number;
  height: number;
  data: any;
  categories: Categories;
  fieldConfig: any;
  replaceVariables: InterpolateFunction;
  aggregatorType: AggregatorType;
  showTotals: boolean;
  showSubTotals: boolean;
  subTotalColumn: number;
  showRowPercentage: boolean;
  showColumnPercentage: boolean;
  hideEmpty: boolean;
  onChange: any;
  rowMax?: number;
  colMax?: number;
  colorOptions: ColorOptions;
  textAlign: TextAlignOptions;
}

export enum AggregatorType {
  COUNT = 'Count',
  SUM = 'Sum',
  AVERAGE = 'Average',
  MEDIAN = 'Median',
  MIN = 'Minimum',
  MAX = 'Maximum',
  SUM_AS_FRANCTION_OF_TOTAL = 'Sum as a fraction of Totals',
  SUM_AS_FRANCTION_OF_ROWS = 'Sum as a fraction of rows',
  SUM_AS_FRANCTION_OF_COLUMNS = 'Sum as a fraction of columns',
  COUNT_AS_FRANCTION_OF_TOTAL = 'Count as a fraction of Totals',
  COUNT_AS_FRANCTION_OF_ROWS = 'Count as a fraction of rows',
  COUNT_AS_FRANCTION_OF_COLUMNS = 'Count as a fraction of columns',
}

export enum SortingOrders {
  NONE = 'none',
  KEY_ASC = 'key_a_to_z',
  KEY_DESC = 'key_z_to_a',
  TOTAL_ASC = 'value_a_to_z',
  TOTAL_DESC = 'value_z_to_a',
}

export interface Categories {
  rowsCategories: any[];
  rowOrder: SortingOrders;
  columnsCategories: any[];
  colOrder: SortingOrders;
  valueCategory: any[];
  categoriesValueFilter: any;
  categoriesValues: any;
  filterable: any;
}

export interface Options {
  categories: Categories;
  aggregatorType: AggregatorType;
  showTotals: boolean;
  showSubTotals: boolean;
  subTotalColumn: number;
  showRowPercentage: boolean;
  showColumnPercentage: boolean;
  hideEmpty: boolean;
  colMax?: number;
  rowMax?: number;
  headerColor?: string;
  headerBgColor?: string;
  subTotalHeaderBgColor?: string;
  columnTextAlign?: TextAlignment;
  rowTextAlign?: TextAlignment;
  valueTextAlign?: TextAlignment;
}

export enum CellDisplayMode {
  None = 'none',
  ColorText = 'color-text',
  ColorBackground = 'color-background',
}

export interface CustomFieldConfig {
  cellDisplayMode: CellDisplayMode;
}

export const LIMITS = {
  DEFAULT_ROW_LIMIT: 100,
  DEFAULT_COL_LIMIT: 60,
  VIEW_MODE_ROW_LIMIT: 150,
  VIEW_MODE_COL_LIMIT: 150,
  VIEW_MODE_NODE_LIMIT: 20000,
};
